<template>
	<div class="prototype_interface">
		<div class="header">
			<div class="header_item projects"></div>
			<div class="header_item metadata">
				<div class="label">Zoom Levels <span class="keyboard_shortcut">shift-option 1-4 to save</span></div>
				<div class="zoom_levels">
					<ul>
						<li v-for="(zoom_level, index) in zoom_levels"
							v-bind:class="{selected: zoom == zoom_level}"
							v-on:click="on_click_zoom(index, zoom_level, $event)"
						>
							{{zoom_level * 100}}% <span class="keyboard_shortcut">(option {{index + 1}})</span>
						</li>
					</ul>
				</div>
			</div>
			<div class="header_item brush size">
				<div class="label">Brush size</div>
				<div class="">{{brush_size}}px</div>
			</div>

			<div class="header_item voice_commands_toggle" v-bind:class="{disabled:!is_voice_commands_available}" v-on:click="toggle_voice_commands">
				<div class="label">Voice Commands</div>
				<div class="toggle" v-bind:class="{active:is_voice_commands_on}"></div>
			</div>
			<div class="header_item tools">
				<div class="tool" v-for="tool in tools"
					v-bind:class="{active:selected_tool == tool.id}"
					v-on:click="on_select_tool(tool.id)"
				>
					<div class="label">{{tool.label}}</div>
					<div class="icon">{{tool.icon}}</div>
				</div>
				<div class="tool mode">
					<div class="label">Mode</div>
					<div class="icon">{{modes[selected_mode].label}} (mode {{selected_mode + 1}})</div>
					<div class="mode_list">
						<ul>
							<li v-for="(mode, index) in modes"
								v-bind:class="{selected: index == selected_mode}"
								v-on:click="selected_mode = index"
							>
								{{mode.label}} (mode {{index + 1}})
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>

		<div class="quick_switch">
			<ul>
				<li v-for="(image, index) in images" v-bind:class="{selected: image.url == selected_image.url}" v-on:click="load_image(image.url)">
					<div class="label">{{image.label}}</div>
					<div class="keyboard_shortcut">{{index + 1}}</div>
				</li>
			</ul>
		</div>

		<div class="object_counter"
			v-bind:class="{on_changed:drawing_objects.length % 2 == 0, on_changed_alt:drawing_objects.length % 2 == 1}"
			v-bind:style="{'border-color':stroke_color}"
		>
			<div class="label">Object</div>
			<div class="value">{{drawing_objects.length + 1}}</div>
		</div>

		<div class="current_zoom_level">{{zoom * 100}}%</div>

		<div ref="konva_stage" class="konva_stage"
			v-bind:class="{
				cursor_zoom_in: selected_tool == 'zoom' && !is_option_pressed,
				cursor_zoom_out: selected_tool == 'zoom' && is_option_pressed,
				cursor_hand: selected_tool == 'pan' || is_spacebar_pressed
			}"
		>
			<div class="cursor" v-if="cursor_position && selected_tool == 'pencil'" v-bind:style="{left: this.cursor_position.x + 'px', top: this.cursor_position.y + 'px', width:brush_size + 'px', height:brush_size + 'px'}"></div>

			<v-stage ref="stage" :config="konva_stage" v-bind:style="{width:selected_image.width+'px', height:selected_image.height+'px'}"></v-stage>
		</div>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

import Artyom from "artyom.js";
import EventBus from '../../event_bus/event_bus.js';

export default {
	name: 'prototype_interface',
	components: {
	},
	props: {
	},
	computed: {
	},
	data() {
		return {
			selected_image: {
				url: null,

				image_width: 0, //original image width (before zoom)
				image_height: 0,

				width: 0, //set once the image has been loaded
				height: 0,
			},
			images: [
				{url: '/static/images/prototype_interface.jpg', label: 'cells'},
				{url: '/static/images/prototype_interface_heat.jpg', label: 'heat'},
				{url: '/static/images/prototype_interface_red.jpg', label: 'red'},
				{url: '/static/images/prototype_interface_uv.jpg', label: 'uv'},
			],

			konva_stage: {
				width: 600, //will be set to viewport dimensions
				height: 600
			},

			cursor_position: null,

			brush_size: 15,
			brush_size_max: 250,
			brush_size_previous: 15,

			zoom: 1, //16, //based on 1 = 100%, 16 = 1600%
			zoom_levels: [1,4,8,16], // [1,2,4,8,16] //new Map([0,1], [1,2])
			zoom_level_max: 16,

			viewport: null,
			stage: null,
			image_layer: null,
			draw_layer: null,

			is_drawing: false,
			is_brush_sizing: false,
			is_panning: false,
			is_erasing: false,
			is_zooming: false,

			is_shift_pressed: false,
			is_option_pressed: false,
			is_spacebar_pressed: false,

			start_pointer_position: null,
			last_pointer_position: null,

			//all navigator and screen position formulas are based on this as it is the larger set of data
			scroll: {
				left: 0,
				top: 0,

				max: {
					left: 0,
					top: 0
				}
			},
			scroll_start: {
				left: 0, //used as reference when spacebar scrolling
				top: 0
			},

			selected_tool: 'pencil',
			tools: [
				{id: 'pencil', label: 'Pencil', icon: '/', on_click:function(tool) {
					this.on_select_tool(tool.id);
				}},
				{id: 'pan', label: 'Pan', icon: 'HAND', on_click:function(tool) {
				}},
				{id: 'zoom', label: 'Zoom', icon: '@', on_click:function(tool) {
				}},
			],
/* //using Map() this way causes a bug, might have to set to null to start and update this in mounted()
			tools: new Map(
				['pencil', {
					label: 'Pencil',
					icon: '/'
				}],
				['pan', {
					label: 'Pan',
					icon: 'J'
				}],
				['zoom', {
					label: 'Zoom',
					icon: '@'
				}]
			),
*/

			modes: [
				{label: 'Lipid'},
				{label: 'Cancer'},
				{label: 'Enteroendocrine'},
				{label: 'Pancreatic islets'},
			],
			selected_mode: 0,

			drawing_objects: [], //eventually an array of objects with drawing data, for now, just a faked array
			stroke_color: '#55ff55',


//voice command parameters
			log: [],

			voice_control: null,
			voice_control_name: 'PIXSURE',

			is_voice_commands_on: false, //true,
			is_voice_commands_available: false,
			is_matched_command: false,
			is_actively_listening: false, //flag to know if Artyom is listening or processing, changes tons during audio

			commands: [
				{
					description: 'mode {any number 1-4}',
					smart: true,
					indexes: ['mode *'],
					action: function(index, mode) {
						EventBus.$emit('switch_mode', {mode: mode});
					}
				},
				{
					description: 'voice off',
					indexes: ['voice off'],
					action: (index)=>EventBus.$emit('voice')
				},
/*
				{
					description: 'undo',
					indexes: ['undo'],
					action: (index)=>EventBus.$emit('undo')
				},
*/
			],

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		//other js library to consider  https://gist.github.com/mjackson/5311256
		//https://css-tricks.com/converting-color-spaces-in-javascript/

//FUTURE: look into konvaJS filters for HSL https://konvajs.org/api/Konva.Filters.html#.HSL

		HSLToHex(h, s, l) {
			s /= 100;
			l /= 100;

			let c = (1 - Math.abs(2 * l - 1)) * s,
			x = c * (1 - Math.abs((h / 60) % 2 - 1)),
			m = l - c/2,
			r = 0,
			g = 0,
			b = 0;

			if (0 <= h && h < 60) {
				r = c; g = x; b = 0;
			} else if (60 <= h && h < 120) {
				r = x; g = c; b = 0;
			} else if (120 <= h && h < 180) {
				r = 0; g = c; b = x;
			} else if (180 <= h && h < 240) {
				r = 0; g = x; b = c;
			} else if (240 <= h && h < 300) {
				r = x; g = 0; b = c;
			} else if (300 <= h && h < 360) {
				r = c; g = 0; b = x;
			}

			// Having obtained RGB, convert channels to hex
			r = Math.round((r + m) * 255).toString(16);
			g = Math.round((g + m) * 255).toString(16);
			b = Math.round((b + m) * 255).toString(16);

			// Prepend 0s, if necessary
			if (r.length == 1) {r = "0" + r;}
			if (g.length == 1) {g = "0" + g;}
			if (b.length == 1) {b = "0" + b;}

			return "#" + r + g + b;
		},
		RGBToHSL(r, g, b) {
			// Make r, g, and b fractions of 1
			r /= 255;
			g /= 255;
			b /= 255;

			// Find greatest and smallest channel values
			let cmin = Math.min(r,g,b),
			cmax = Math.max(r,g,b),
			delta = cmax - cmin,
			h = 0,
			s = 0,
			l = 0;

			// Calculate hue
			// No difference
			if (delta == 0)
			h = 0;
			// Red is max
			else if (cmax == r)
			h = ((g - b) / delta) % 6;
			// Green is max
			else if (cmax == g)
			h = (b - r) / delta + 2;
			// Blue is max
			else
			h = (r - g) / delta + 4;

			h = Math.round(h * 60);

			// Make negative hues positive behind 360°
			if (h < 0)
			h += 360;

			// Calculate lightness
			l = (cmax + cmin) / 2;

			// Calculate saturation
			s = delta == 0 ? 0 : delta / (1 - Math.abs(2 * l - 1));

			// Multiply l and s by 100
			s = +(s * 100).toFixed(1);
			l = +(l * 100).toFixed(1);

			return {h:h, s:s, l:l}; //[h,s,l]; //"hsl(" + h + "," + s + "%," + l + "%)";
		},

		//********************************************
		pick_random_stroke_color() {
			this.stroke_color = this.HSLToHex(Math.floor(Math.random() * 255), Math.floor(Math.random() * 70) + 30, Math.floor(Math.random() * 50) + 30);
		},

		//********************************************
		respond(message, shutup) {
			//randomly pick a message
			if (Array.isArray(message)) {message = message[Math.floor(Math.random() * message.length)];}

			this.log.unshift(message);

			//speak text as well
			if (this.is_voice_commands_on && this.voice_control.speechSupported()) {
				//shutup
				if (shutup) {this.voice_control.shutUp();}

				this.voice_control.say(message);
			}
		},

		//********************************************
		on_select_tool(id) {
			this.selected_tool = id;
		},

		//********************************************
		start_drawing(e) {
			let is_shift_pressed = (e.evt && e.evt.shiftKey) ? true : false;
			let is_option_pressed = (e.evt && e.evt.altKey) ? true : false;
			let is_control_pressed = (e.evt && e.evt.ctrlKey) ? true : false;

			if (this.selected_tool == 'pencil' && is_shift_pressed) {
				//shift - click and drag left / right to change brush size
				this.is_brush_sizing = true;
				this.brush_size_previous = this.brush_size;
			} else if (this.selected_tool == 'pan' || this.is_spacebar_pressed) {
				//spacebar - click and drag to pan around image
				this.is_panning = true;

				this.scroll_start = {
					left: this.$refs['konva_stage'].scrollLeft,
					top: this.$refs['konva_stage'].scrollTop
				};
			} else if (this.selected_tool == 'zoom') {
				this.is_zooming = true;
			} else if (this.selected_tool == 'pencil') {
				this.is_drawing = true;
				this.is_erasing = (is_option_pressed) ? true : false;
			}

			this.start_pointer_position = this.stage.getPointerPosition();
			this.last_pointer_position = this.stage.getPointerPosition();
		},

		//********************************************
		stop_drawing(e) {
			if (this.is_zooming) {
				let new_zoom_level = this.zoom;
//future, observe where the click happened and center on zoom
				if (this.is_option_pressed) {
					//zoom out
					if (new_zoom_level > 1) {new_zoom_level--;}
				} else {
					//zoom in
					if (new_zoom_level < this.zoom_level_max) {new_zoom_level++;}
				}

				if (this.zoom != new_zoom_level) {this.on_change_zoom(new_zoom_level);}
			}

			this.is_brush_sizing = false;
			this.is_panning = false;
			this.is_drawing = false;
			this.is_zooming = false;

//			var position = this.stage.getPointerPosition();
			this.start_pointer_position = null;
		},

		//********************************************
		on_move_drawing(e) {
			let position = this.stage.getPointerPosition();

			if (this.is_brush_sizing) {
				//adjusting the brush size
				let new_brush_size = Math.floor(this.brush_size_previous + position.x - this.start_pointer_position.x);
				if (new_brush_size < 1) {new_brush_size = 1;}
				if (new_brush_size > this.brush_size_max) {new_brush_size = this.brush_size_max;}

				this.brush_size = new_brush_size;
			} else if (this.is_panning) {
				//panning
				let scroll_position = {
					left: Math.floor(this.scroll_start.left + this.start_pointer_position.x - position.x),
					top: Math.floor(this.scroll_start.top + this.start_pointer_position.y - position.y)
				};

				this.$refs['konva_stage'].scrollLeft = scroll_position.left;
				this.$refs['konva_stage'].scrollTop = scroll_position.top;

				this.on_scroll();

				//get an updated position after the scroll
				position = this.stage.getPointerPosition();
			}

			//update cursor (after pan just in case there is a new scroll position)
			this.cursor_position = {
				x: (position.x + this.scroll.left),// / this.zoom,
				y: (position.y + this.scroll.top)// / this.zoom
			};

			//if not drawing, then skip the rest of this function
			if (!this.is_drawing) {return;}

			let local_position = {
				x: (this.last_pointer_position.x + this.scroll.left) / this.zoom, // - this.image_layer.x()
				y: (this.last_pointer_position.y + this.scroll.top) / this.zoom // - this.image_layer.y()
			};

			let new_position = {
				x: (position.x + this.scroll.left) / this.zoom, // - this.image_layer.x()
				y: (position.y + this.scroll.top) / this.zoom // - this.image_layer.y()
			};

			let stroke_width = Math.floor(this.brush_size * (1 / this.zoom));
			if (stroke_width < 1) {stroke_width = 1;}

			let line = new Konva.Line({
				points: [local_position.x, local_position.y, new_position.x, new_position.y],
				stroke: this.stroke_color,
				strokeWidth: stroke_width,
				lineJoin: 'round',
				lineCap: 'round',
				listening: false,
				globalCompositeOperation: (this.is_erasing) ? 'destination-out' : 'source-over', //https://konvajs.org/docs/sandbox/Free_Drawing.html
			});
			this.draw_layer.add(line);
			this.draw_layer.draw();

			this.last_pointer_position = position;
		},

		//********************************************
		on_double_click(e) {
			e = e.evt; //focus just on the mouse event

			//zoom in as well (if shift key was pressed)
			let is_shift_pressed = (e.shiftKey) ? e.shiftKey : false;

			if (is_shift_pressed) {
				if (this.zoom > this.zoom_level_max / 2) {
					//zoom to 100%
					this.on_change_zoom(1);
				} else {
					//zoom to
					this.on_change_zoom(this.zoom_level_max);
				}
			}
		},

		//********************************************
		//position is percentage % 0-1
		scroll_to(position) {
			if (position.left < 0) {position.left = 0;}
			if (position.left > 1) {position.left = 1;}
			if (position.top < 0) {position.top = 0;}
			if (position.top > 1) {position.top = 1;}

			//should really save this info instead of looking it up each time (on image load and resize)
			this.$refs['konva_stage'].scrollLeft = position.left * this.scroll.max.left;
			this.$refs['konva_stage'].scrollTop = position.top * this.scroll.max.top;

			this.on_scroll();
		},

		//********************************************
		on_scroll(e) {
			this.scroll.left = this.$refs['konva_stage'].scrollLeft;
			this.scroll.top = this.$refs['konva_stage'].scrollTop;

			//transform stage container
			this.stage.container().style.transform = `translate(${this.scroll.left}px, ${this.scroll.top}px)`;

			//counter stage position
			this.stage.x(-this.scroll.left);
			this.stage.y(-this.scroll.top);

			this.stage.draw();
		},

		//********************************************
		add_new_object() {
			//change stroke color (to fake a new layer)
			this.pick_random_stroke_color();

			//save the object (for now, just fake we saved the image info)
			this.drawing_objects.push(true);

			this.respond([
				'New object',
				'Added object',
				'Add new object',
				'New object added'
			], true);
		},

		//********************************************
		increase_brush_size() {
			if (this.brush_size >= this.brush_size_max) {return;}

			let brush_change = Math.floor(this.brush_size / 10) + 1;

			let new_brush_size = this.brush_size + brush_change;
			if (new_brush_size > this.brush_size_max) {new_brush_size = this.brush_size_max;}

			this.brush_size = new_brush_size;
		},

		//********************************************
		decrease_brush_size() {
			if (this.brush_size <= 1) {return;}

			let brush_change = Math.floor(this.brush_size / 10) + 1;

			let new_brush_size = this.brush_size - brush_change;
			if (new_brush_size < 1) {new_brush_size = 1;}

			this.brush_size = new_brush_size;
		},

		//********************************************
		on_next_mode() {
			let index = this.selected_mode + 1;

			if (index >= this.modes.length) {index = 0;}

			this.selected_mode = index;

			let mode_name = this.modes[this.selected_mode].label + ' Mode';
			this.respond(mode_name, true);
		},

		//********************************************
		on_previous_mode() {
			let index = this.selected_mode - 1;
			if (index < 0) {index = this.modes.length - 1;}

			this.selected_mode = index;

			let mode_name = this.modes[this.selected_mode].label + ' Mode';
			this.respond(mode_name, true);
		},

		//********************************************
		on_click_zoom(index, level, e) {
			let is_shift_pressed = (e.shiftKey) ? true : false;

			if (is_shift_pressed) {
				//save this present
				this.on_set_zoom(index, this.zoom);
			} else {
				//change zoom level
				this.on_change_zoom(level);
			}
		},

		//********************************************
		on_change_zoom(level) {
			this.zoom = level;

//something is broken in the update function. for now, just reset the scroll to 0
			this.$refs['konva_stage'].scrollLeft = 0;
			this.$refs['konva_stage'].scrollTop = 0;

			this.scroll.left = this.$refs['konva_stage'].scrollLeft;
			this.scroll.top = this.$refs['konva_stage'].scrollTop;

			this.stage.container().style.transform = `translate(${this.scroll.left}px, ${this.scroll.top}px)`;

			//counter stage position
			this.stage.x(-this.scroll.left);
			this.stage.y(-this.scroll.top);

			this.update();
		},

		//********************************************
		on_set_zoom(index, level) {
			this.zoom_levels[index] = level;
			this.$forceUpdate();
		},

		//********************************************
		load_image(url) {
			Konva.Image.fromURL(url, (img)=>{
				this.selected_image.image_width = img.getAttr('image').width;
				this.selected_image.image_height = img.getAttr('image').height;
				this.selected_image.url = url;

				img.setAttrs({
					x: 0,
					y: 0
				});

				this.image_layer.clear();

				this.image_layer.imageSmoothingEnabled(false); //show pixels when zoomed in
				this.image_layer.add(img);

				this.update();
			});
		},

		//********************************************
		toggle_voice_commands() {
			if (this.is_voice_commands_on) {
				this.stop_voice_commands();
			} else {
				this.start_voice_commands();
			}
		},

		//********************************************
		start_voice_commands() {
			if (this.is_voice_commands_available) {this.voice_control.obey();}
			this.is_voice_commands_on = true;
		},

		//********************************************
		stop_voice_commands() {
			if (this.is_voice_commands_available) {this.voice_control.dontObey();}
			this.is_voice_commands_on = false;
		},

		//********************************************
		update() {
			//image update
			this.selected_image.width = (this.selected_image.image_width * this.zoom);
			this.selected_image.height = (this.selected_image.image_height * this.zoom);

			//update the size of the stage container
			this.stage.container().style.width = this.selected_image.width + 'px';
			this.stage.container().style.height = this.selected_image.height + 'px';

			//image
			if (this.selected_image.width > 0 && this.selected_image.height > 0) {
				this.image_layer.scale({
					x: this.zoom,
					y: this.zoom
				});

				this.image_layer.draw();
				this.image_layer.cache(); //also helps create pixel render
			}

			this.draw_layer.scale({
				x: this.zoom,
				y: this.zoom
			});
			this.draw_layer.draw();

			this.stage.draw();

			//now update items that needed the stage to be rendered

			//viewport dimensions
			this.scroll.max.left = this.selected_image.width - this.viewport.clientWidth;
			this.scroll.max.top = this.selected_image.height - this.viewport.clientHeight;

			this.stage.draw();
		},

		//********************************************
		on_resize() {
			//change stage size to match viewport
			this.stage.width(this.viewport.clientWidth);
			this.stage.height(this.viewport.clientHeight);

			this.update();
		},

		//********************************************
		on_keydown(e) {
			let is_shift_pressed = (e.shiftKey) ? true : false;
			let is_option_pressed = (e.altKey) ? true : false;
			let is_control_pressed = (e.ctrlKey) ? true : false;
//e.key letter name

			//1-9
			//quick switch and zoom
			let number_key_code = parseInt(e.code.replace('Digit', ''), 10);
			if (number_key_code >= 1 && number_key_code <= 9) {
				number_key_code--; //change to zero index

				if (is_option_pressed) {
					//zoom
					if (number_key_code < this.zoom_levels.length) {
						if (is_shift_pressed) {
							this.on_set_zoom(number_key_code, this.zoom);
						} else {
							this.on_change_zoom(this.zoom_levels[number_key_code]);
						}
					}
				} else {
					//quick switch
					//if not already loaded
					if (number_key_code < this.images.length && this.selected_image.url != this.images[number_key_code].url) {this.load_image(this.images[number_key_code].url);}
				}

				e.preventDefault();
				return true;
			}

			switch(e.keyCode) {
				case 65: //a
				case 13: //enter
					this.add_new_object();
					e.preventDefault();
					break;
				case 18: //option
					this.is_option_pressed = true;
					break;
				case 32: //spacebar
					this.is_spacebar_pressed = true;
					e.preventDefault();
					break;
				case 49: //49-57 (1-9)
					//handled above, should never get here
					break;
				case 66: //b
					if (this.selected_tool == 'pencil') {
						if (is_shift_pressed) {
							//brush smaller
							this.decrease_brush_size();
						} else {
							//brush bigger
							this.increase_brush_size();
						}
					} else {
						this.on_select_tool('pencil');
					}

					e.preventDefault();
					break;
				case 68: //d
				case 88: //x
					if (is_shift_pressed) {
						//previous mode
						this.on_previous_mode();
					} else {
						//next mode
						this.on_next_mode();
					}

					e.preventDefault();
					break;
				case 72: //h
					this.on_select_tool('pan');
					e.preventDefault();
					break;
				case 86: //v
					this.toggle_voice_commands();
					e.preventDefault();
					break;
				case 90: //z
					if (is_shift_pressed) {
						//zoom out
						if (this.zoom > 1) {this.on_change_zoom(this.zoom - 1);}
					} else {
						//zoom in
						if (this.zoom < this.zoom_level_max) {this.on_change_zoom(this.zoom + 1);}
					}
/*
					if (this.selected_tool == 'zoom') {
						if (is_shift_pressed) {
							//zoom out
							if (this.zoom > 1) {this.on_change_zoom(this.zoom - 1);}
						} else {
							//zoom in
							if (this.zoom < this.zoom_level_max) {this.on_change_zoom(this.zoom + 1);}
						}
					} else {
						this.on_select_tool('zoom');
					}
*/
					e.preventDefault();
					break;
			}
		},

		//********************************************
		on_keyup(e) {
			switch(e.keyCode) {
				case 18: //option
					this.is_option_pressed = false;
					break;
				case 32: //spacebar
					this.is_spacebar_pressed = false;
					e.preventDefault();
					break;
			}
		}
	},
	destroyed() {
		//konva_stage
		if (this.$refs['konva_stage']) {this.$refs['konva_stage'].removeEventListener('scroll', this.on_scroll);}

		//keyboard events
		window.removeEventListener('keydown', this.on_keydown);
		window.removeEventListener('keyup', this.on_keyup);

		//voice commands
		EventBus.$off('switch_mode');

		//stop voice control
		if (this.voice_control) {this.voice_control.fatality();}
	},
	mounted() {
		//stage
		this.viewport = this.$refs['konva_stage'];
		this.stage = this.$refs['stage'].getStage();

		// add the layer to the stage
		this.image_layer = new Konva.Layer();
		this.stage.add(this.image_layer);

		this.draw_layer = new Konva.FastLayer({ //using fast layer to avoid
//			clearBeforeDraw: false,
			listening: false
		});
		this.draw_layer.imageSmoothingEnabled(false); //show pixels when zoomed in
		this.stage.add(this.draw_layer);

		//load image
		this.load_image(this.images[0].url);

		//events
		this.$refs['konva_stage'].addEventListener('scroll', this.on_scroll);

		this.image_layer.on('mousedown touchstart', this.start_drawing);
		this.image_layer.on('mouseup touchend', this.stop_drawing);
		this.image_layer.on('mousemove touchmove', this.on_move_drawing);

		this.image_layer.on('dblclick', this.on_double_click);

		//keyboard events
		window.addEventListener('keydown', this.on_keydown);
		window.addEventListener('keyup', this.on_keyup);

		//reseize event
		this.on_resize();
		window.addEventListener('resize', this.on_resize); //add timer into this to gain better performance on resize (instead of every pixel resized)

		//voice commands
		EventBus.$on('switch_mode', (parameters)=>{
			let mode = parameters.mode.toLowerCase().trim();

			//convert known strings to numbers
			let alternatives = {
				'one':1,
				'to':2,
				'too':2,
				'two':2,
				'three':3,
				'for':4,
				'ate':8,
				'zero':0
			};

			if (typeof alternatives[mode] != 'undefined') {mode = alternatives[mode];}

			//by default, pass the string
			let mode_name = 'Mode ' + mode;

			//determine if this is a number
			if (isNaN(mode) == false) {
				let mode_index = parseInt(mode, 10);

				//found a match
				if (mode_index >= 1 && mode_index <= this.modes.length) {
					mode_name = this.modes[mode_index - 1].label + ' mode';

					this.selected_mode = mode_index - 1;
				} else {
					mode_name = 'Mode ' + mode + ' not available';
				}
			} else {
				mode_name = 'Mode ' + mode + ' is currently not available';
			}

			this.respond(mode_name);
		});
		EventBus.$on('voice', (parameters)=>{
			this.respond('Voice Command Off');
			this.stop_voice_commands();
		});

		//setup voice control
		this.voice_control = new Artyom();
		this.is_voice_commands_available = (this.voice_control.recognizingSupported()) ? true : false;

		//voice commands
		this.voice_control.addCommands(this.commands);

		//start listening
		if (this.is_voice_commands_available) {
			this.voice_control.when('COMMAND_MATCHED', ()=>{
				this.is_matched_command = true;
				this.is_actively_listening = false;
			});
			this.voice_control.when('NOT_COMMAND_MATCHED', ()=>{
				this.is_matched_command = false;
				this.is_actively_listening = false;
			});
			this.voice_control.when('TEXT_RECOGNIZED', ()=>{this.is_actively_listening = true;});

			this.voice_control.when('ERROR', (error)=>{
console.error(error);
//				this.is_actively_listening = false;
			});

			this.voice_control.initialize({
				lang:"en-GB",
				continuous: true,
				listen: true, // Start listening after this
				speed: 1, // speed of speech
				mode: "quick", // This parameter is not required as it will be normal by default

				soundex: false,
				name: 'picture', //not Pixure since the voice recognition has a hard time recognizing that
				executionKeyword: null,

				debug: true, // Show what recognizes in the Console
			});

			this.is_actively_listening = true;
		}
	},
	watch: {
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


