<template>
	<div class="prototype_ui">
		<h1>Flex Menu</h1>
		<h5>using flex basis which makes animation smooth (since we're hiding / showing content)</h5>

		<div class="flex_menu">
			<div class="box_button previous">
				&lt;
				<div class="label">previous task</div>
			</div>
			<div class="box_label">
				<div class="label">works great if only a single button at the end</div>
			</div>
			<div class="box_button next">
				&gt;
				<div class="label">next task</div>
			</div>
		</div>

		<div class="flex_menu">
			<div class="box_button previous">
				&lt;
				<div class="label">previous task</div>
			</div>
			<div class="box_label">
				<div class="label">next button is hard to click if add button is open</div>
			</div>
			<div class="box_button next">
				&gt;
				<div class="label">next task</div>
			</div>
			<div class="box_button add">
				+
				<div class="label">add new task</div>
			</div>
		</div>

		<div class="flex_menu has_delay">
			<div class="box_button previous">
				&lt;
				<div class="label">previous task</div>
			</div>
			<div class="box_label">
				<div class="label">HAS DELAY - across all buttons</div>
			</div>
			<div class="box_button next">
				&gt;
				<div class="label">next task</div>
			</div>
			<div class="box_button add">
				+
				<div class="label">add new task</div>
			</div>
		</div>

		<div class="flex_menu">
			<div class="box_button previous">
				&lt;
				<div class="label">previous task</div>
			</div>
			<div class="box_label">
				<div class="label">HAS DELAY ON END ONLY - only the add button has the delay</div>
			</div>
			<div class="box_button next">
				&gt;
				<div class="label">next task</div>
			</div>
			<div class="box_button add has_delay">
				+
				<div class="label">add new task</div>
			</div>
		</div>

		<div class="flex_menu_grow_shrink">
			<div class="box_button previous">
				&lt;
				<div class="label">previous task</div>
			</div>
			<div class="box_label">
				<div class="label">LABEL IS HIDDEN - has soft opacity transition (breaks if add button has long label)</div>
			</div>
			<div class="box_button next">
				&gt;
				<div class="label">next task</div>
			</div>
			<div class="box_button add has_delay">
				+
				<div class="label">add new task with a long label</div>
			</div>
		</div>

		<h1>Add Multiple Items</h1>
		<h5>click on the far right box to add a child box. click on left boxes to return to that parent</h5>

		<div class="flex_menu_app">
			<div class="box" v-for="(box, index) of boxes" v-on:click.stop="on_click(box)" v-bind:class="{selected: box == selected_box}">
				<div class="label">
					<div class="number">{{box.number}}</div>
					<div class="summary">{{box.label}}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

import EventBus from '../../event_bus/event_bus.js';

export default {
	name: 'prototype_ui',
	components: {
	},
	props: {
	},
	computed: {
	},
	data() {
		return {
			selected_box: null,
			boxes: [
				{number: 1, label: 'task summary'}
			],
			box_number: 1,

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		on_click(box) {
			let index = this.boxes.findIndex((data) => data === box);

			let labels = [
				'simple task',
				'determine data analytics techniques',
				'note to the team',
				'collection of data files'
			];

			if (index == this.boxes.length - 1) {
				//add to end
				this.box_number++;
				this.boxes.push({
					number: this.box_number,
					label: labels[Math.floor(Math.random() * labels.length)]
				});

				this.selected_box = this.boxes[this.boxes.length - 1];
			} else {
				//go back
				this.boxes.splice(index + 1, this.boxes.length - index - 1);
				this.selected_box = this.boxes[this.boxes.length - 1];
			}
		}
	},
	destroyed() {
	},
	mounted() {
	},
	watch: {
	},
	created() {
		this.selected_box = this.boxes[0];
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


