//*********************************************************
//extra functions that extend core javascript functionaity
//otherwise, consider mixin, plugins, or filters for custom app functionality


//*****************
//extend object to have a clone() function
//recursively makes a unique copy of an object
//no references are shared and the cloned object can change properties without changes to the parent
//
//https://stackoverflow.com/questions/728360/how-do-i-correctly-clone-a-javascript-object
//
if (!Object.clone) {
	Object.clone = function(obj) {
		var copy;

		//handle the 3 simple types and null or undefined
		if (obj == null || typeof obj != "object") {return obj;}

		//date
		if (obj instanceof Date) {
			copy = new Date();
			copy.setTime(obj.getTime());

			return copy;
		}

		//array
		if (obj instanceof Array) {
			copy = [];

			for (var i=0, len=obj.length; i < len; i++) {
				copy[i] = Object.clone(obj[i]);
			}

			return copy;
		}

		//object
		if (obj instanceof Object) {
			copy = {};

			for (var attr in obj) {
				if (obj.hasOwnProperty(attr)) {copy[attr] = Object.clone(obj[attr]);}
			}

			return copy;
		}

		throw new Error("unable to clone object. type is not supported.");
	}
}

//*****************
//extend object to have a count() function
if (!Object.count) {
	Object.count = function(obj) {
		//undefined object
		if (obj === undefined) {return 0;}

		//use built-in keys if available
//		  if (Object.keys) {return Object.keys(obj).length;}

		//otherwise use this routine
		var length = 0;
		for (var key in obj) {
			if (obj.hasOwnProperty(key)) {
				length++;
			}
		}

		return length;
	}
}

//*****************
// add Object.keys to Object definition if doesn't exist
if (!Object.keys) {
	Object.keys = function(obj) {
		var keys = [];

		if (obj === undefined) {return undefined;}

		for (var key in obj) {
			if (obj.hasOwnProperty(key)) {
				keys.push(key);
			}
		}
		return keys;
	}
}

//*****************
// add Object.values to Object definition if doesn't exist
if (!Object.values) {
	Object.values = function(obj) {
		var values = [];

		if (obj === undefined) {return [];}

		for (var key in obj) {
			if (obj.hasOwnProperty(key)) {
				values.push(obj[key]);
			}
		}
		return values;
	}
}

//*****************
// add Object.is_array to Object definition if doesn't exist
// similar to jquery $.isArray()
if (!Object.is_array) {
	Object.is_array = function(variable) {
		return (Object.prototype.toString.call(variable) === '[object Array]') ? true : false;
	}
}

//*****************
//add console functionality if it doesn't exist
if (typeof window.console == 'undefined') {
	//core functionality but won't do anything since there is no console
	window.console = {
		log: function(msg) {},
		error: function(msg) {},
		info: function(msg) {},
		warn: function(msg) {},

		time: function(msg) {},
		timeEnd: function(msg) {}
	};
}

//console.error
if (typeof window.console.error == 'undefined') {window.console.error = function(msg) {window.console.log('ERROR', msg);}}

//console.info
if (typeof window.console.info == 'undefined') {window.console.info = function(msg) {window.console.log('INFO', msg);}}

//console.warn
if (typeof window.console.warn == 'undefined') {window.console.warn = function(msg) {window.console.log('WARNING', msg);}}

//console.time
if (typeof window.console.time == 'undefined') {window.console.time = function(name) {window.console.warn('console.time(' + name + ') not supported');}}

//console.timeEnd
if (typeof window.console.timeEnd == 'undefined') {window.console.timeEnd = function(name) {window.console.warn('console.timeEnd(' + name + ') not supported');}}


//fallback for requestAnimationFrame (used in the scrollto library)
window.requestAnimationFrame = window.requestAnimationFrame
	|| window.mozRequestAnimationFrame
	|| window.webkitRequestAnimationFrame
	|| window.msRequestAnimationFrame
	|| function(f){return setTimeout(f, 1000/60)}; // simulate calling code 60

window.cancelAnimationFrame = window.cancelAnimationFrame
	|| window.mozCancelAnimationFrame
	|| function(requestID){clearTimeout(requestID)}; //fall back


// https://github.com/WICG/EventListenerOptions/blob/gh-pages/explainer.md#feature-detection
window.supports_passive = false;
try {
    let opts = Object.defineProperty({}, "passive", {
        get: function() {
            window.supports_passive = true;
        }
    });
    window.addEventListener("test", null, opts);
} catch (e) {};




