// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue';
import App from './App';
import router from './router';

import filters from './filters.js';
import utilities from './utilities.js';

//import {EventBus} from './event_bus/event_bus.js';
//import EventBus from './event_bus/event_bus.js';

Vue.config.productionTip = false;

//https://konvajs.org/docs/vue/
import VueKonva from 'vue-konva';
Vue.use(VueKonva);

//https://github.com/devstark-com/vue-circle-slider
import VueCircleSlider from 'vue-circle-slider';
Vue.use(VueCircleSlider);

//import VueAnime from './vue-anime.js';
//Vue.use(VueAnime);


//*********************************************************
//primary data store for entire app. no need for a full vuex implementation on this project

var data_store = {
	google_analytics_app_name: 'NCSA Prototypes',
	google_analytics_id: (process.env.NODE_ENV && process.env.NODE_ENV == 'production') ? '' : '', //if in production mode, then connect to google analytics

	//used for phylogeny prototype
	selected_blocks: [],
//	phylogeny_relapse_data: {},
	phylogeny_show_table: false,
	phylogeny_selected_tier: null,
	phylogeny_proportion_sets: {},
	phylogeny_proportion_id: 'primary',

	menu: [
/*
		{id: 'about', label: 'About'},
		{
			id: 'linkedin',
			label: 'LinkedIn',
			button_label: 'Linked<img src="static/images/logo_linkedin.png" class="icon"> Profile',
			url:'https://www.linkedin.com/in/'
		},
*/
	]
};


//*********************************************************
/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	components: { App },
	template: '<App/>',

	data: data_store,

	methods: {
		//********************************************
		//view this page
		// accepts {name: string, project: string}
		// will use the current datetime or filter if either of those parameters are not supplied
		view_page(parameters) {
			if (!parameters) {return;}

			var name = (typeof parameters['name'] != 'undefined') ? parameters['name'] : 'gesture';
//			var project = (typeof parameters['project'] != 'undefined') ? parameters['project'] : '';

//			var query = this.convert_filters_to_query(filters);

			//change router now
			this.$router.push({
				name: name,
				params: {
//					project: project
				},
//				query: query
			});
		},

		//********************************************
		//open url
		open_url(url) {
			//open url
			if (url.search('mailto:') > -1) {
				//email
				window.location.href = url;

				//google analytics
				this.send_google_analytics_pageview('email', url.replace('mailto:', ''));
			} else {
				//link
				window.open(url, '_blank');

				//google analytics
				this.send_google_analytics_pageview('link', url);
			}
		},

		//********************************************
		//open url
		send_google_analytics_pageview(page_title, path) {
			if (this.google_analytics_id && typeof this.$gtag == 'function') {
				this.$gtag('config', this.google_analytics_id, {
					'page_title': page_title,
					'page_path': path
				});
			}
		},

		//********************************************
		//based on modernizer function
		// https://github.com/Modernizr/Modernizr/blob/master/feature-detects/touchevents.js
		//
		// NOTE: this is NOT a perfect solution and some devices won't be recognized
		// https://stackoverflow.com/questions/4817029/whats-the-best-way-to-detect-a-touch-screen-device-using-javascript#4819886
		//
		// interesting read on why this may be the wrong way to approach this (test for human interaction instead of device capabilities)
		// https://codeburst.io/the-only-way-to-detect-touch-with-javascript-7791a3346685
		//
		is_touch_device: function() {
			return (('ontouchstart' in window) || window.DocumentTouch && document instanceof DocumentTouch) ? true: false;
		}
	},
	mounted() {
		//********************************************
		//setup google analytics handler
		//this is usually handled BEFORE VUE is loaded. added here to allow google_analytics_id in config and newer gtag() functionality
		//otherwise, look at https://github.com/MatteoGabriele/vue-analytics for a more packaged solution

		if (this.google_analytics_id) {
			//********************************************
			//load google script
			let google_analytics = document.createElement('script');
			google_analytics.setAttribute('async', '');
			google_analytics.setAttribute('src', 'https://www.googletagmanager.com/gtag/js?id=' + this.google_analytics_id);

			document.head.appendChild(google_analytics);

			//register functions
			window.dataLayer = window.dataLayer || [];
			this.$gtag = function() {window.dataLayer.push(arguments);}

			this.$gtag('js', new Date());
			this.$gtag('config', this.google_analytics_id, {'send_page_view': false}); //load, but do not automatically send a page view

			//********************************************
			//setup router
			let parse_router_data = function(obj) {
				var project = (obj.params && obj.params.project && obj.params.project != 'all') ? obj.params.project : '';
				var page_title = (project) ? project : obj.name;

				return {page_title: page_title, path: obj.path};
			}

			//router handler
			this.$root.$router.afterEach((to, from) => {
				//google analytics
				var {page_title, path} = parse_router_data(to);
				this.send_google_analytics_pageview(page_title, path);
			});

			//google analytics
			//send current page
			var {page_title, path} = parse_router_data(this.$root.$router.history.current);
			this.send_google_analytics_pageview(page_title, path);
		}
	}
});


