			let skills = {};
			for (let i=0; i < this.resources.list.length; i++) {
//				let resource_skills = [];
				for (let skill of this.resources.list[i].skills) {
					if (!skills[skill]) {
						skills[skill] = {
							label: skill,
							resources: []
						}
					}

					skills[skill].resources.push(this.resources.list[i].label);
//					resource_skills.push(skills[skill]);
				}

//				this.resources.list[i].skills = resource_skills;
			}

			return skills; //Object.values(skills);
		}
