							description: 'Mayo started off by using the example of the Alzheimer’s Disease Neuroimaging Initiative (ADNI) and the internal Aging and Dementia Imaging Research (ADIR) lab as well curated, accessible databases, with HIPAA compliance, universal file formats, annotation, and so on.<br><br>The goal would be to have an integrated Mayo Clinic Neurology data platform that serves a similar purpose, but can incorporate the extensive and heterogenous neurophysiological timeseries data. For some study types, like EMG, no universal file format exists. Obviously, we cannot design models if we cannot access the data, annotate it, share it in an anonymized fashion, etc. Mayo does not anticipate much help from Google when it comes to developing file formats, or developing a software interface to interact with Neurology data. Whatever will end up in the cloud will likely have to be built by Neurology. This is an area where Mayo Neurology needs immediate assistance.',
							tags: ['data processing', 'neurological', 'universal file format'],
							list_items: [
								{label: 'Define approach', filetype: '', description: 'UIUC will draw up some preliminary approaches', conversation: []},
								{label: 'File formats', filetype: 'data', description: 'Mayo will share a rough data inventory and some details on file formats (e.g. MEF)', conversation: []},
								{label: 'Second meeting prep', filetype: '', description: 'This will be fleshed out more at the second visit followed by a white paper', conversation: []},
								{label: 'White paper', filetype: '', description: '', conversation: []},
							],
							fake_list_items_data: [
							],
