						{
							label: 'automated data transfer from the neurological lab instruments to local and cloud storage',
							description: 'UIUC brought up several good points regarding cloud only, local only, and hybrid approaches. They have a custom GPU-heavy grid that is optimized for deep learning. It would cost about $100,000 per month to have a similar setup in the cloud. They have previously developed models locally and then deployed the ‘final’ model to the cloud later, instead of developing on the cloud. This fits with Mayo’s plan to have a local data and processing footprint which interfaces with the cloud. Assuming, of course, that is ‘allowed’ under the agreement.<br><br>UIUC is experienced when it comes to transferring large amounts of data. They transferred ~350 terabytes as part of the Grand Challenge, for instance. They routinely transfer and process a lot of astronomical data. UIUC would be able to help Mayo Neurology build a system to transfer data from the EEG, EMG, etc machines to local storage where it can be converted to the correct file format, annotated, etc before being pushed to the cloud. The data can also be transferred to the UIUC grid if we with to build models there.',
							tags: ['automated data transfer', 'cloud storage'],
							list_items: [
								{label: 'Cloud arrangement details', filetype: '', description: 'Mayo needs to find out the details of the cloud arrangement with Google. To what extent can the cloud offering be customized to meet neurology needs? UIUC can help optimize the cloud setup based on our needs too.', conversation: []},
								{label: 'Mayo local and cloud setup', filetype: '', description: 'Mayo needs to ensure that they can have both local and cloud setups. This includes ‘local’ processing as either on site or at UIUC.', conversation: []},
								{label: 'Locked to Google standards?', filetype: '', description: 'Mayo needs to find out if we will be locked in to using Google’s compute/coding/ML standards, since this would impact the data structure and local approach.', conversation: []},
