						},
						{
							label: 'scientific insights using machine learning models',
							description: 'The long-term goal of gaining novel insights into neurological disease in order to develop new treatments or even cures will require models that can process multimodal data consisting of different timescales (e.g. EEG, PET) and different spatial scales (e.g. genetic or ‘omics’, structural imaging, behavioral data) over the lifetime of the patient (i.e. longitudinally). It is not feasible to immediately move into this space and doing so would be more likely to result in a Google Flu Trends snafu than anything else.<br><br>Instead, simpler models that focus on a single modality or timescale and a well-defined problem will be pursued. The analogy of the senses was used: we need models that can recognized patterns on images (sight), or in speech samples (hearing), etc. This will naturally lead to more complicated models as we will be able to extract clinically meaningful latent information from each test and then fuse these into datasets that can be used in subsequent models.',
