						{
							label: 'Project 1: Machine Learning for Variant Interpretation',
							description: 'There exists a critical need for innovative methods to aid in the interpretation of next-generation sequencing results in the clinical laboratory. New tools are needed to drive down turnaround time and decrease the FTE necessary for identifying reportable results in our test offerings.<br><br>With the emergence of AI, there is the hope and hype that data challenges in the laboratory can be quickly addressed. The reality is machine learning algorithms can significantly improve decision making when substantial datasets are available for algorithm training and testing.<br><br><strong>Goal:</strong> Train a machine learning algorithm using the clinically interpreted datasets we have accrued with the NGSHM test. The algorithms (NN, XGBoost, SVM, KNN, or HMM) will be trained using a standard split train and test approach. Namely, cross-validation on the training set will be used to select and build the optimal interpretation tool, whose performance can be verified on the test set.',
