						{
							label: 'Project 2: Advanced RNA splicing detection',
							description: 'RNA sequencing offers a powerful, high-throughput method of characterizing the functional effects of DNA variation and enables the detection of certain variant types not identifiable by exome sequencing. Recently published studies have shown that complementary RNA sequencing improves diagnostic yield of DNA testing by 10-35% in select disease areas. A small pilot study in CIM’s Translational Omics Program has also indicated that increased diagnostic yield can be obtained across the broad phenotypic spectrum of RUGD patients seen in CIM’s Individualized Medicine Clinic. Transcriptome interrogation of these Mayo patients have disambiguated putative splice variants, identified genes with significantly aberrant expression levels, identified deep intronic pathogenic cryptic splicing events, and identified pathogenic deletions and inversions not reported by current standard clinical testing.<br><br>In this project, we propose to use the RNA and DNA sequencing on 300 patients to establish a deep learning method for detection of aberrant and putatively disease causal alternative splicing. Data from both in silico predictions of the primary DNA sequencing, will be integrated with observed low-level splicing in RNA, known and predicted transcript structures, and other relevant annotations to model and predict ‘outlier’ like splicing events that may mediate rare disease.',
							tags: [],
							list_items: [
