						{
							label: 'Project 3: Comprehensive Multi-omic Profiling in Rare Disease Patients',
							description: 'Increasingly DNA and RNA sequencing are being used as standard practice to diagnosis patients suspected and undiagnosed, rare Mendelian disease. DNAseq has yielded solve rates near 25% for diagnostic odyssey patients, and several studies have shown improved diagnostic rates, by as much as 35%, when combining DNAseq with RNAseq. Recently, the transformative study RADIANT was funded to RNA sequence >300 unsolved, rare disease cases. This study includes multifaceted analysis, focused on DNA variant & CNV detection, aberrant expression, splicing and fusion transcription analysis.<br><br>Analysis of the data includes several testing modalities aimed at diagnosis and disease characterization that will be described in detail: <ol><li>Methylation-based outlier detection will be performed using novel methods in a heterogeneous rare disease cohort.</li><li>Integrative multiomic analysis of DNA variants, RNA outlier expression and splicing, and DNA methylation will be developed to leverage complementary datasets when evaluating cases.</li><li>Methylation signature for a disease will be developed and evaluated using a combination of traditional methylation analyses and advanced machine learning algorithms.</li></ol>',
