							label: 'Project 4: Machine Learning for Variant Interpretation part II: SAVI + Lit',
							description: 'There exists a critical need for innovative methods to aid in the interpretation of next-generation sequencing results in the clinical laboratory. New tools are needed to drive down turnaround time and decrease the FTE necessary for identifying reportable results in our test offerings. We have created a rules-based tool to aid in the near automated interpretation of hereditary variants, with an initial focus on a select set of genes which may be interrogated in predictive sequencing projects.<br><br>Where SAVI is deficient is in the integration of intelligent systems designed to extract relevant information from the literature. SAVI relies on data that has already been curated and cataloged in existing genetic disease and variant databases. There are, however, emerging tools designed to provide rapid and intuitive query of the literature (including full text), to identify studies relevant to genetic-specific queries. Genomenon is a company who has created a tool Mastermind that supports this type of query. Here we propose to evaluate the ability of Mastermind and explore the integration of this tool with SAVI to create more robust variant interpretations, advance the methods towards greater and greater automation, and decrease the time per variant review.',
