			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		hide_intro() {
			this.show_intro = false;
		},

		//********************************************
		toggle_intro() {
			this.show_intro = !this.show_intro;
		},

		//********************************************
		on_toggle_solo() {
			this.view_solo = !this.view_solo;
		},

		//********************************************
		on_toggle_search_bar() {
			this.view_search_bar = !this.view_search_bar;
		},

		//********************************************
		on_show_search() {
			this.show_search = true;
			this.$refs['search'].focus();
		},

		//********************************************
		on_hide_search() {
			this.show_search = false;
//			this.$refs['search'].blur();
		},

		//********************************************
		on_conversation_add_message() {
			let message = this.form.conversation.new_message.replace(/(\r\n|\n|\r)/gm, '<br />');
			message = message.replace(/(\<br \/\>){2,}/gm, '<br class="double" />'); //replace 2+ line breaks with a double line break only

			//if no typed message, try adding a conversation example (if available) and then cancel
			if (!message) {
				if (!this.selected_list_item) {this.on_click_conversation_panel();}
				return;
			}

			let list_to_post_to = (this.selected_list_item) ? this.selected_list_item : this.selected_list;

			//create array if it doesn't exist
			if (!list_to_post_to.conversation) {list_to_post_to.conversation = [];}

			//username
			message = message.replace(/(\@[\w]+)/gm, '<span class="username">$1</span>');

			list_to_post_to.conversation.push({
				name: 'Me',
				message: message
			});

			this.clear_conversation_message();
		},

		//********************************************
		clear_conversation_message() {
			this.form.conversation.new_message = '';
		},

		//********************************************
		has_slack_id() {
			//determine if a conversation has been started for the selected_list or selected_list_item
			return (!this.selected_list_item && this.selected_list.conversation.length > 0) || (this.selected_list_item && this.selected_list_item.conversation.length > 0);
		},

		//********************************************
		on_add_group() {
			this.groups.push({
				label: 'Data analytics ' + this.groups.length + Math.floor(Math.random() * 99),
				lists: []
			});
		},

		//********************************************
		on_select_group_last() {
			this.on_select_group(this.groups[this.groups.length - 1]);
		},

		//********************************************
		on_toggle_group_panel() {
			this.view_group_panel = !this.view_group_panel;
		},

		//********************************************
		//eventually, change this to show a method to find a group...for now, just cycle through the groups
		on_choose_group() {
			let index = 0;

			if (this.selected_group) {
				index = this.groups.indexOf(this.selected_group);
			}

			index++;
			if (index > this.groups.length) {index = 0;}

			this.on_select_group(this.groups[index]);
		},

		//********************************************
		on_select_group(group) {
//			if (this.selected_group == group) {return;}

			if (group && this.selected_group != group) {
				this.selected_group = group;
				this.lists = this.selected_group.lists;

				this.clear_conversation_message();
			} else {
				//unselect the group
				this.selected_list = null;
				this.selected_list_item = null;

				this.selected_group = null;
				this.lists = null;

				this.view_conversation_panel = false;
			}
		},

		//********************************************
		toggle_add_list_popup(override) {
			this.view_add_list_popup = (typeof override == 'boolean') ? override : !this.view_add_list_popup;

/*
			//auto popuplate fake data if available
			if (this.view_add_list_popup && this.selected_group && this.selected_group.fake_list_data && this.selected_group.fake_list_data.length > 0) {
				//get next
				let fake_list_data = this.selected_group.fake_list_data[0];

				this.form.add_list.label = (fake_list_data.label) ? fake_list_data.label : '';
				this.form.add_list.description = (fake_list_data.description) ? fake_list_data.description : '';
				this.form.add_list.list_items = (fake_list_data.list_items) ? fake_list_data.list_items : [];
				this.form.add_list.conversation = (fake_list_data.conversation) ? fake_list_data.conversation : [];
			}
*/
		},

		//********************************************
		on_add_list_preset(preset) {
			//add a copy
			this.on_add_list(Object.assign({}, Object.clone(preset)));
		},

		//********************************************
		on_add_list(data) {
			//only can add lists when a group is selected, otherwise, list would not be associated with any group
			if (!this.selected_group) {return;}

			let list = {};

			if (data && data.label != '') {
				//use a copy of the preset or form data when
				list = Object.assign({
					list_items: [],
					conversation: []
				}, Object.clone(data));

				data.label = '';
				data.description = '';
			} else if (this.selected_group && this.selected_group.fake_list_data && this.selected_group.fake_list_data.length > 0) {
				//if still have example data, add those
				list = this.selected_group.fake_list_data.shift();
			} else {
				//start faking the data
				let list_items = [];

				let titles = [
					'Discovery',
					'Design Ideas',
					'Rapid Prototype',
					'Alternative Idea',
					'Data',
					'Image Processing',
					'Scope / Proposal'
				];
				let title = titles[Math.floor(Math.random() * titles.length)];

				//pick a few items
				let item_labels = [
					{label: 'We need a collaborative method to tag', filetype: ''},
					{label: 'Sample image marked with Epithelial cells', filetype: 'photo'},
					{label: 'Explore UI selection methods', filetype: ''},
					{label: 'Determine UX workflow for tagging images', filetype: ''},
					{label: 'Sample XML data', filetype: 'data'},
					{label: 'Plan project phases', filetype: ''},
					{label: 'Prototype UI ideas', filetype: ''},
					{label: '3D layers', filetype: 'photo'}
				];

				let quantity = Math.floor(Math.random() * (item_labels.length - 2)) + 1;

				for (let i=0; i<quantity; i++) {
					list_items.push(item_labels.splice(Math.floor(Math.random() * item_labels.length), 1)[0]);
				}

				//for the first list, don't auto populate
				if (this.lists.length == 0) {
					list_items = [];
				} else {
					this.on_add_skill();
				}

				list = {
					label: title,
					list_items: list_items,
					conversation: []
				};
			}

			//add the list
			this.lists.push(list);

			this.hide_intro();
			this.toggle_add_list_popup(false);
		},

		//********************************************
		on_remove_list(list) {
			let index = this.lists.indexOf(list);
			if (index == -1) {return;}

			if (this.selected_list == list) {
				this.selected_list_item = null;
				this.view_conversation_panel = false;
			}

			this.lists.splice(index, 1);
		},

		//********************************************
		toggle_add_topic_popup(override, list) {
			this.view_add_topic_popup = (typeof override == 'boolean') ? override : !this.view_add_topic_popup;
			this.selected_topic_list = (list) ? list : null;
		},

		//********************************************
		on_add_list_item(data) {
			if (!this.selected_topic_list) {return;}

			let list_item = {};

			if (data && data.label != '') {
				//use a copy of the preset or form data when
				list_item = Object.assign({
					filetype: '',
					conversation: []
				}, Object.clone(data));

				data.label = '';
				data.description = '';
			} else if (this.selected_topic_list.fake_list_items_data && this.selected_topic_list.fake_list_items_data.length > 0) {
				//if still have example data, add those
				list_item = this.selected_topic_list.fake_list_items_data.shift();
			} else {
				//start faking the data
				let labels = [
					{label: 'We need a collaborative method to tag', filetype: '', conversation: []},
					{label: 'Sample image marked with Epithelial cells', filetype: 'photo', conversation: []},
					{label: 'Explore UI selection methods', filetype: '', conversation: []},
					{label: 'Determine UX workflow for tagging images', filetype: '', conversation: []},
					{label: 'Sample XML data', filetype: 'data', conversation: []},
					{label: 'Plan project phases', filetype: '', conversation: []},
					{label: 'Prototype UI ideas', filetype: '', conversation: []},
					{label: '3D layers', filetype: 'photo', conversation: []}
				];

				list_item = labels[Math.floor(Math.random() * labels.length)];
			}

			//add the list item
			this.selected_topic_list.list_items.push(list_item);

			this.on_add_skill();
			this.toggle_add_topic_popup(false);
		},

		//********************************************
		on_remove_list_item(list_item, list) {
			let index = list.list_items.indexOf(list_item);
			if (index == -1) {return;}

			list.list_items.splice(index, 1);

			this.view_conversation_panel = false;
		},

		//********************************************
		on_select_list_item(list_item, list) {
			this.selected_list_item = list_item;

			this.selected_list = list;
			this.view_conversation_panel = true;

//			this.clear_conversation_message();
		},

		//********************************************
		on_select_list(list) {
			if (list) {
				if (this.selected_list == list && this.selected_list_item) {
					this.selected_list_item = null;
				} else if (this.selected_list == list && this.view_conversation_panel) {
					this.selected_list = null;
					this.selected_list_item = null;
					this.view_conversation_panel = false;
				} else {
					this.selected_list = list;
					this.selected_list_item = null;
					this.view_conversation_panel = true;
				}

//				this.clear_conversation_message();
			} else {
				this.view_conversation_panel = !this.view_conversation_panel;
			}
		},

		//********************************************
		on_click_conversation_panel() {
			//determine if there are any sample thread data still left to add
			if (!this.selected_list || !this.selected_list.fake_conversation_data || this.selected_list.fake_conversation_data.length == 0) {return;}

			//move the example message to the conversation
			this.selected_list.conversation.push(this.selected_list.fake_conversation_data.shift());
		},

		//********************************************
		toggle_add_skill_popup(override) {
			this.view_add_skill_popup = (typeof override == 'boolean') ? override : !this.view_add_skill_popup;
		},

		//********************************************
		on_toggle_skill() {
			this.view_skill_panel = !this.view_skill_panel;
			this.hide_intro();
		},

		//********************************************
		on_add_skill(available_skills) {
			if (!available_skills) {available_skills = Object.keys(this.available_skills);}

			let skill = available_skills[Math.floor(Math.random() * available_skills.length)];
			this.skills.recommended.set(skill, {label: skill});

			this.$forceUpdate(); //bad way of doing it...this map bug is fixed in vue 3
		},

		//********************************************
		on_add_skill_applied(skill) {
			this.toggle_add_skill_popup(false);
			if (!skill.label) {return;}

			this.skills.applied.set(skill.label, {label: skill.label});
			this.skills.recommended.delete(skill.label);

			this.on_add_resource(skill.label);

			this.form.add_skill.label = '';

			this.$forceUpdate(); //bad way of doing it...this map bug is fixed in vue 3
		},

		//********************************************
		on_remove_skill_applied(skill) {
			this.skills.applied.delete(skill.label);
			this.skills.recommended.set(skill.label, {label: skill.label});

			this.$forceUpdate(); //bad way of doing it...this map bug is fixed in vue 3
		},

		//********************************************
		on_toggle_resource() {
			this.view_resource_panel = !this.view_resource_panel;
			this.hide_intro();
		},

		//********************************************
		on_add_resource(skill) {
			let team = this.resources.list;

			//if sent a skill, only add a resource that matches that skill
			if (skill) {
				//make sure we have a match now that we have a form with custom labels
				if (!this.available_skills[skill]) {return;}

				let team = this.resources.list.filter((person)=>{
					return this.available_skills[skill].resources.includes(person.label);
				});
			}

			//reduce to people that are NOT already in the list
			team = team.filter((person)=>{
				return (!this.resources.recommended.has(person.label)) ? true : false;
			});
			team = team.filter((person)=>{
				return (!this.resources.applied.has(person.label)) ? true : false;
			});

			//add another resource
			if (team.length > 0) {
				let person = team[Math.floor(Math.random() * team.length)];
				this.resources.recommended.set(person.label, {label: person.label, skills: person.skills});
			}

			this.$forceUpdate(); //bad way of doing it...this map bug is fixed in vue 3
		},

		//********************************************
		on_add_resource_applied(resource) {
			this.resources.applied.set(resource.label, {label: resource.label});
			this.resources.recommended.delete(resource.label);

			this.on_add_skill(resource.skills);
/*
			//only add a new skill if NONE or randomly
			if (this.skills.recommended.size == 0 || Math.random() > .5) {
			} else {
				this.$forceUpdate(); //bad way of doing it...this map bug is fixed in vue 3
			}
*/
		},

		//********************************************
		on_remove_resource_applied(resource) {
			this.resources.applied.delete(resource.label);
			this.resources.recommended.set(resource.label, {label: resource.label});

			this.$forceUpdate(); //bad way of doing it...this map bug is fixed in vue 3
		},

		//********************************************
		set_conversation_filter(filter) {
			if (this.selected_conversation_filter == filter) {return;}

			this.selected_conversation_filter = filter;
		},
	},
	destroyed() {
	},
	mounted() {
	},
	watch: {
	},
	created() {
		//select the first group
		if (this.groups.length > 0) {
			this.on_select_group(this.groups[0]);
		}

		//set conversation filter
		if (this.conversation_filters) {
			this.selected_conversation_filter = this.conversation_filters[0];
		}
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


