<template>
	<div class="prototype prototype_histogram">
		<h1>Select by color and brightness</h1>
		<div class="description"></div>

		<div class="konva_stage" v2-bind:style="{backgroundImage: 'url(' + images[selected_image].url + ')'}">
			<v-stage ref="stage" :config="konva_stage"></v-stage>
		</div>
<!--
		<div class="setting_group">
			<div class="setting radio_buttons">
				<div class="label">Choose Image</div>

				<div class="buttons">
					<div v-for="(image, index) of images" class="radio_button" v-bind:class="{selected: selected_image == index}">
						<input type="radio" :id="'image_' + index" :value="index" v-model="selected_image" v-on:click="selected_image = index"><label :for="'image_' + index">Image {{index}}</label></input>
					</div>
				</div>
			</div>
		</div>
-->

		<div class="color_picker histogram" v-on:mousedown="start_drag($event, 'histogram')" v-on:mouseup="stop_drag($event)">
			<div class="min" v-bind:style="{left: (histogram.min / histogram.total * 100) + '%'}"><div class="label">{{histogram.min}}</div></div>
			<div class="max" v-bind:style="{left: (histogram.max / histogram.total * 100) + '%'}"><div class="label">{{histogram.max}}</div></div>
			<svg ref="histogram_svg" />
		</div>

		<div class="color_picker brightness" v-on:mousedown="start_drag($event, 'brightness')" v-on:mouseup="stop_drag($event)">
			<div class="min" v-bind:style="{left: (brightness.min / brightness.total * 100) + '%'}"><div class="label">{{brightness.min}}</div></div>
			<div class="max" v-bind:style="{left: (brightness.max / brightness.total * 100) + '%'}"><div class="label">{{brightness.max}}</div></div>
			<svg ref="brightness_svg" />
		</div>

		<div class="color_picker wide">
			<h4>Presets</h4>
			<div class="button" v-on:click="select_preset(preset.parameters)" v-for="(preset, index) in presets">{{preset.label}}</div>
<!--
			<div class="button" v-on:click="()=>{histogram.min=167;histogram.max=330;brightness.min=18;brightness.max=100;}">Lipid Cell</div>
			<div class="button" v-on:click="()=>{histogram.min=272;histogram.max=360;brightness.min=19;brightness.max=100;}">Lipid Cell (Outside)</div>
			<div class="button" v-on:click="()=>{histogram.min=83;histogram.max=153;brightness.min=20;brightness.max=100;}">Green</div>
			<div class="button" v-on:click="()=>{histogram.min=291;histogram.max=341;brightness.min=40;brightness.max=90;}">Pink (upper right)</div>
-->
		</div>
	</div>
</template>

<script>
//*********************************************************

//import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

export default {
	name: 'prototype_histogram',
	components: {
	},
	props: {
	},
	computed: {
		min_snap_radius: function() {
			return (this.settings['brush_size'].value >= this.settings['snap_radius'].value) ? this.settings['brush_size'].value : this.settings['snap_radius'].value;
		}
	},
	data() {
		return {
			//local data
/*
			slider_config: {
				progressColor: "#1dbd80",
				knobColor: "#1dbd80",

				circleWidthRel: 10,
				progressWidthRel: 5,
				knobRadiusRel: 7,
				side: 105
			},
*/
			selected_image: 0,
			images: [
				{url: '/static/images/prototype_histogram.jpg'},
//				{url: '/static/images/20180327_MATday15_Tumor2cm_1mm_FOV450um_s3_m1_z156_20us.jpg'},
//				{url: '/static/images/20180327_MATday15_Tumor2cm_1mm_FOV500um_s2_m2_z156_20us.jpg'},
			],

			slider_name: null,
			slider_parameter: null,

			presets: [
				{label: 'Lipid Cell', parameters: {
					histogram: {min: 167, max: 330},
					brightness: {min: 18, max: 100}
				}},
				{label: 'Lipid Cell (Outside)', parameters: {
					histogram: {min: 272, max: 360},
					brightness: {min: 18, max: 100}
				}},
				{label: 'Green', parameters: {
					histogram: {min: 83, max: 153},
					brightness: {min: 20, max: 100}
				}},
				{label: 'Pink (upper right)', parameters: {
					histogram: {min: 291, max: 341},
					brightness: {min: 40, max: 90}
				}},
				{label: 'Yellow', parameters: {
					histogram: {min: 44, max: 62},
					brightness: {min: 28, max: 60}
				}},
				{label: 'Everything', parameters: {
					histogram: {min: 0, max: 360},
					brightness: {min: 0, max: 100}
				}},
			],

			histogram: {
				min: 168,
				max: 319,
				total: 360,
				parameter: null //reference during a drag across this slider to know which parameter to control
			},
			brightness: {
				min: 18,
				max: 100,
				total: 100,
				parameter: null
			},

			konva_stage: {
				width: 600,
				height: 600
			},

			stage: null,
			image_layer: null,
			drawing_layer: null,
			mask_layer: null,

			selector_box: null, //reference to the selector box (when holding the mouse down)
			is_painting: false,

			start_pointer_position: null,
			last_pointer_position: null,
			max_distance: 0,

			timer: null,

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		//other js library to consider  https://gist.github.com/mjackson/5311256
		//https://css-tricks.com/converting-color-spaces-in-javascript/

//FUTURE: look into konvaJS filters for HSL https://konvajs.org/api/Konva.Filters.html#.HSL

		HSLToHex(h, s, l) {
			s /= 100;
			l /= 100;

			let c = (1 - Math.abs(2 * l - 1)) * s,
			x = c * (1 - Math.abs((h / 60) % 2 - 1)),
			m = l - c/2,
			r = 0,
			g = 0,
			b = 0;

			if (0 <= h && h < 60) {
				r = c; g = x; b = 0;
			} else if (60 <= h && h < 120) {
				r = x; g = c; b = 0;
			} else if (120 <= h && h < 180) {
				r = 0; g = c; b = x;
			} else if (180 <= h && h < 240) {
				r = 0; g = x; b = c;
			} else if (240 <= h && h < 300) {
				r = x; g = 0; b = c;
			} else if (300 <= h && h < 360) {
				r = c; g = 0; b = x;
			}

			// Having obtained RGB, convert channels to hex
			r = Math.round((r + m) * 255).toString(16);
			g = Math.round((g + m) * 255).toString(16);
			b = Math.round((b + m) * 255).toString(16);

			// Prepend 0s, if necessary
			if (r.length == 1) {r = "0" + r;}
			if (g.length == 1) {g = "0" + g;}
			if (b.length == 1) {b = "0" + b;}

			return "#" + r + g + b;
		},
		RGBToHSL(r, g, b) {
			// Make r, g, and b fractions of 1
			r /= 255;
			g /= 255;
			b /= 255;

			// Find greatest and smallest channel values
			let cmin = Math.min(r,g,b),
			cmax = Math.max(r,g,b),
			delta = cmax - cmin,
			h = 0,
			s = 0,
			l = 0;

			// Calculate hue
			// No difference
			if (delta == 0)
			h = 0;
			// Red is max
			else if (cmax == r)
			h = ((g - b) / delta) % 6;
			// Green is max
			else if (cmax == g)
			h = (b - r) / delta + 2;
			// Blue is max
			else
			h = (r - g) / delta + 4;

			h = Math.round(h * 60);

			// Make negative hues positive behind 360�
			if (h < 0)
			h += 360;

			// Calculate lightness
			l = (cmax + cmin) / 2;

			// Calculate saturation
			s = delta == 0 ? 0 : delta / (1 - Math.abs(2 * l - 1));

			// Multiply l and s by 100
			s = +(s * 100).toFixed(1);
			l = +(l * 100).toFixed(1);

			return {h:h, s:s, l:l}; //[h,s,l]; //"hsl(" + h + "," + s + "%," + l + "%)";
		},

		//********************************************
		toggle_setting_info(index, force_value) {
			this.settings[index].is_active = (force_value !== undefined) ? force_value : !this.settings[index].is_active;
		},

		//********************************************
		get_value(name) {
			return this.settings[name].value;
		},

		//********************************************
		start_timer() {
			this.stop_timer();

			this.timer = setTimeout(()=>{
//console.error('timer done!');

//				this.clear_drawing_layer();
//				this.circle_helper.stroke('#888');

//				this.drawing_layer.batchDraw();

				//circle should already be at the start spot, no need to do anything else
			}, this.settings['delay'].value);
		},

		//********************************************
		stop_timer() {
			if (this.timer) {
				clearTimeout(this.timer);
				this.timer = null;

//				this.circle_helper.radius(this.min_snap_radius); //this.settings['snap_radius'].value);
//				this.circle_helper.stroke(this.settings['snap_radius'].color); //delay
			}
		},

		//********************************************
		clear_drawing_layer() {
//			if (this.drawing_layer) {this.drawing_layer.clear();}
		},

		//********************************************
		start_drag(e, slider_name) {
			var target = e.target;

			this.slider_name = slider_name;
			this.slider_parameter = 'min';

			var total = this[this.slider_name].total;

			//determine which property to control
			var cursor_percent = e.offsetX / 360 * 100; //cursor location
			var min_percent = this[this.slider_name].min / total * 100;
			var max_percent = this[this.slider_name].max / total * 100;

			if (cursor_percent <= min_percent) {
				this.slider_parameter = 'min';
			} else if (cursor_percent >= max_percent) {
				this.slider_parameter = 'max';
			} else {
				//determine which is closest
				if (max_percent - cursor_percent < cursor_percent - min_percent) {this.slider_parameter = 'max';}
			}

			['mousemove', 'touchmove'].forEach((event)=>{
				target.addEventListener(event, this.drag_move);
			});
		},

		//********************************************
		stop_drag(e) {
			var target = e.target;

			['mousemove', 'touchmove'].forEach((event)=>{
				target.removeEventListener(event, this.drag_move);
			});
		},

		//********************************************
		drag_move(e) {
			var new_value = (e.offsetX / 360 * this[this.slider_name].total);

			if (this.slider_parameter == 'min' && new_value > this[this.slider_name].max) {
				new_value = this[this.slider_name].max;
			} else if (this.slider_parameter == 'max' && new_value < this[this.slider_name].min) {
				new_value = this[this.slider_name].min;
			}

			if (new_value != this[this.slider_name][this.slider_parameter]) {
				this[this.slider_name][this.slider_parameter] = Math.floor(new_value * 10) / 10;
				this.refresh_mask();
			}
		},

		//********************************************
		refresh_mask() {
			if (!this.selector_box) {return;}

			var context = this.image_layer.getContext();
			var image_data = context.getImageData(this.selector_box.x(), this.selector_box.y(), this.selector_box.width(), this.selector_box.height());

			var number_of_pixels = image_data.data.length;

			//test for histogram
			for (var i=0; i<number_of_pixels; i+=4) {
				var hsl = this.RGBToHSL(image_data.data[i], image_data.data[i + 1], image_data.data[i + 2]);
//console.log(image_data.data[i] + ', ' + image_data.data[i + 1] + ', ' + image_data.data[i + 2] + ' :: ' + hsl);
//				if (image_data.data[i] > 80 || image_data.data[i + 1] > 80 || image_data.data[i + 2] > 80) {

				if ((hsl.l >= this.brightness.min && hsl.l <= this.brightness.max) && (hsl.h >= this.histogram.min && hsl.h <= this.histogram.max)) {
					image_data.data[i] = 255; //should really paint the entire mask once instead of changing the pixels each time
					image_data.data[i + 1] = 255;
					image_data.data[i + 2] = 0;
					image_data.data[i + 3] = 255;
				} else {
					image_data.data[i + 3] = 0; //make transparent
				}
			}

			this.mask_layer.clear();
			this.mask_layer.getContext().putImageData(image_data, this.selector_box.x(), this.selector_box.y());
		},

		//********************************************
		select_preset(parameters) {
			if (typeof parameters['histogram'].min != undefined) {this['histogram'].min = parameters['histogram'].min;}
			if (typeof parameters['histogram'].max != undefined) {this['histogram'].max = parameters['histogram'].max;}
			if (typeof parameters['brightness'].min != undefined) {this['brightness'].min = parameters['brightness'].min;}
			if (typeof parameters['brightness'].max != undefined) {this['brightness'].max = parameters['brightness'].max;}

			this.refresh_mask();
		}
	},
	mounted() {
		this.stage = this.$refs['stage'].getStage();

		//create layers to the stage
		this.image_layer = new Konva.Layer();
		this.stage.add(this.image_layer); //where the actual images are loaded to

		this.drawing_layer = new Konva.Layer();
		this.stage.add(this.drawing_layer); //the primary drawing layer (where we show the drawing tools)

		this.mask_layer = new Konva.Layer();
		this.stage.add(this.mask_layer); //mask layer is where we paint the mask pixels

		//draw the histogram svg (color histogram)
		var svgns = "http://www.w3.org/2000/svg";

		//change to gradient instead of lots of rectangles
		for (var x=0; x<360; x++) {
			var rect = document.createElementNS(svgns, 'rect');

			var color = this.HSLToHex(x, 100, 50);

			rect.setAttributeNS(null, 'x', x * 1);
			rect.setAttributeNS(null, 'y', 0);
			rect.setAttributeNS(null, 'width', 1); //fixed at 2px wide for now...eventually change to support responsive design
			rect.setAttributeNS(null, 'height', 40);
			rect.setAttributeNS(null, 'fill', color); //'#' + Math.round(0xffffff * Math.random()).toString(16));

			this.$refs['histogram_svg'].appendChild(rect);
		}

		//change to gradient instead of lots of rectangles
		//draw the brightness svg (brightness histogram)
		for (var x=0; x<360; x++) {
			var rect = document.createElementNS(svgns, 'rect');

			var levels = Math.round(0xff * (x / 360)).toString(16);
			if (levels.length == 1) {levels = '0' + levels;}
			var color = '#' + levels + levels + levels; //Math.round(0xffffff * (x / 360)).toString(16);

			rect.setAttributeNS(null, 'x', x * 1);
			rect.setAttributeNS(null, 'y', 0);
			rect.setAttributeNS(null, 'width', 1); //fixed at 2px wide for now...eventually change to support responsive design
			rect.setAttributeNS(null, 'height', 40);
			rect.setAttributeNS(null, 'fill', color); //'#' + Math.round(0xffffff * Math.random()).toString(16));

			this.$refs['brightness_svg'].appendChild(rect);
		}

		//load image
		Konva.Image.fromURL(this.images[0].url, (image)=>{
			image.setAttrs({
				x: 0,
				y: 0
			});

			this.image_layer.add(image);
			this.image_layer.draw();
		});

		//draw everything to stage
		this.stage.draw();

		//events
		this.stage.on('mousedown touchstart', ()=>{
			this.is_painting = true;

			this.start_pointer_position = this.stage.getPointerPosition();
			this.last_pointer_position = this.stage.getPointerPosition();
			this.max_distance = 0;

			this.clear_drawing_layer();

			//create selector box
			if (this.selector_box) {this.selector_box.destroy();}
			this.selector_box = null;

			this.selector_box = new Konva.Rect({
				x: this.start_pointer_position.x,
				y: this.start_pointer_position.y,
				width: 1,
				height: 1,
				stroke: '#ff5',
				strokeWidth: 2,
				dashEnabled: true,
				dash: [2, 1]
			});
			this.drawing_layer.add(this.selector_box);

			this.drawing_layer.batchDraw();
		});

		this.stage.on('mouseup touchend', ()=>{
			this.is_painting = false;
			this.start_pointer_position = null;

			this.refresh_mask();

			this.drawing_layer.batchDraw();
		});

		//core drawing function
		this.stage.on('mousemove touchmove', ()=>{
			var position = this.stage.getPointerPosition();

			if (!this.is_painting) {
				//move cursor
//				this.circle_helper.absolutePosition(position);
//				this.drawing_layer.batchDraw();

				return;
			}

			//determine distance traveled if currently in circle mode or testing for the circle
			//https://ncalculators.com/geometry/length-between-two-points-calculator.htm
			var distance = Math.sqrt(Math.pow(position.x - this.start_pointer_position.x, 2) + Math.pow(position.y - this.start_pointer_position.y, 2));
			if (distance > this.max_distance) {this.max_distance = distance;}

			//update selector box
			this.selector_box.width(Math.abs(position.x - this.start_pointer_position.x));
			this.selector_box.height(Math.abs(position.y - this.start_pointer_position.y));

			var box_position = Object.clone(this.start_pointer_position);
			if (position.x < this.start_pointer_position.x) {box_position.x = this.start_pointer_position.x - this.selector_box.width();}
			if (position.y < this.start_pointer_position.y) {box_position.y = this.start_pointer_position.y - this.selector_box.height();}

			this.selector_box.absolutePosition(box_position);

			this.last_pointer_position = position;

			this.drawing_layer.batchDraw();
		});
	},
	watch: {
		'settings.brush_size.value': function(new_value, old_value) {
			if (this.is_painting) {return;}

			this.circle_helper.radius(new_value / 2);
			this.drawing_layer.batchDraw();
		}
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


