<template>
	<div class="prototype_genomix_dashboard">
		<h1>Genomix Dashboard Prototypes</h1>
		<h6>(click on a dashboard widget to view different behaviors)</h6>

		<h3>Navigation</h3>
		<div class="explanation">widgets are navigation, clicking on a widget will open/close a hidden block with additional data</div>

		<div class="dashboard dashboard_navigation"
			v-bind:class="{has_selected: dashboard_navigation_selected}"
		>
			<div class="component selected_component"
				v-on:click.stop="dashboard_select('dashboard_navigation_selected', null)"
			>
				<template v-if="dashboard_navigation_selected">
					<div class="label" v-html="dashboard_navigation_selected.label"></div>
					<div class="value">{{dashboard_navigation_selected.value}}<span class="percent">%</span></div>
					<div class="details">more information for {{data.label}} goes here</div>
				</template>
			</div>

			<div class="component" v-for="(data,index) in components"
				v-bind:class="{is_selected: dashboard_navigation_selected == data}"
				v-on:click.stop="dashboard_select('dashboard_navigation_selected', data)"
			>
				<div class="label" v-html="data.label"></div>
				<div class="value">{{data.value}}<span class="percent">%</span></div>
			</div>
		</div>

		<h3>Expandable</h3>
		<div class="explanation">expands the widget and minimizes the rest</div>

		<div class="dashboard dashboard_expandable"
			v-bind:class="{has_selected: dashboard_expandable_selected}"
		>
			<div class="component" v-for="(data,index) in components"
				v-bind:class="{is_selected: dashboard_expandable_selected == data}"
				v-on:click.stop="dashboard_select('dashboard_expandable_selected', data)"
			>
				<div class="label" v-html="data.label"></div>
				<div class="value">{{data.value}}<span class="percent">%</span></div>
				<div class="details">more information for {{data.label}} goes here</div>
			</div>
		</div>

		<h3>Inline / Flip</h3>
		<div class="explanation">3D flip to have access to more info on the back. NOTE: doubt IE11 can support this and Safari didn't fully work</div>

		<div class="dashboard dashboard_inline">
			<div class="component" v-for="(data,index) in components"
				v-bind:class="{is_selected: dashboard_inline_selected == data}"
				v-on:click.stop="dashboard_select('dashboard_inline_selected', data)"
			>
				<div class="component_inner">
					<div class="front">
						<div class="label" v-html="data.label"></div>
						<div class="value">{{data.value}}<span class="percent">%</span></div>
					</div>
					<div class="back">
						<div class="details">more information for {{data.label}} goes here</div>
					</div>
				</div>
			</div>
		</div>

		<h3>Direct</h3>
		<div class="explanation">clicking on a widget immediately opens the tab to the deeper data</div>

		<div class="dashboard dashboard_hotspot">
			<div class="component" v-for="(data,index) in components">
				<div class="label" v-html="data.label"></div>
				<div class="value">{{data.value}}<span class="percent">%</span></div>
			</div>
		</div>

		<h3>Overlay</h3>
		<div class="explanation">details open up in an overlay</div>

		<div class="dashboard dashboard_overlay">
			<div class="component" v-for="(data,index) in components"
				v-bind:class="{is_selected: dashboard_overlay_selected == data}"
				v-on:click.stop="dashboard_select('dashboard_overlay_selected', data)"
			>
				<div class="label" v-html="data.label"></div>
				<div class="value">{{data.value}}<span class="percent">%</span></div>

				<div class="overlay"
					v-on:click.stop="dashboard_select('dashboard_overlay_selected', data)"
				>
					<div class="details">more information for {{data.label}} goes here</div>
				</div>
			</div>
		</div>

		<h3>Responsive Width</h3>
		<div class="explanation">responsive width based on browser width. try resizing the browser window. NOTE: width auto scales, but does have a minimum width</div>

		<div class="dashboard dashboard_dynamic">
			<div class="component" v-for="(data,index) in components"
				v-bind:class="{is_selected: dashboard_dynamic_selected == data}"
				v-on:click.stop="dashboard_select('dashboard_dynamic_selected', data)"
			>
				<div class="label" v-html="data.label"></div>
				<div class="value">{{data.value}}<span class="percent">%</span></div>
				<div class="details">more information for {{data.label}} goes here</div>
			</div>
		</div>

<!--
should look at ways to minimize other widgets when expanding the view inline
OR look to reuse the entire block (3D rotate or just quick switch to full widget view)
OR always have widget click to go deep to the other tab (which keeps click area big as it’s the entire widget)
-->
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

import EventBus from '../../event_bus/event_bus.js';

export default {
	name: 'prototype_genomix_dashboard',
	components: {
	},
	props: {
	},
	computed: {
	},
	data() {
		return {
			components: [
				{label: 'Cretinine Levels', value: 25},
				{label: 'Cholesterol', value: 9.4},
				{label: 'Bilirubin', value: 5.3},
				{label: 'Albumin', value: 86},
				{label: 'Cholesterol', value: 17.1},
			],

			dashboard_navigation_selected: null,
			dashboard_expandable_selected: null,
			dashboard_inline_selected: null,
			dashboard_overlay_selected: null,
			dashboard_dynamic_selected: null,

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		dashboard_select(name, value) {
			if (this[name] == value) {
				//deselect if currently selected
				this[name] = null;
			} else {
				//select this item
				this[name] = value;
			}
		}
	},
	destroyed() {
	},
	mounted() {
	},
	watch: {
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


