<template>
	<div ref="prototype_tile" class="prototype_tile">
		<div ref="navigator_stage" class="navigator_stage" v-bind:style="{width:navigator_konva_stage.width+'px', height:navigator_konva_stage.height+'px'}">
			<div class="stats" v-if="tile.complete_pixel_count > 0 && tile.total_pixel_count > 0">
				{{Math.floor(tile.complete_pixel_count / tile.total_pixel_count * 100)}}% Complete
			</div>

			<v-stage ref="navigator_konva_stage" :config="navigator_konva_stage"></v-stage>
		</div>

		<div class="controls">
			<div class="" v-on:click="show_help = true">Help</div>
			<div class="" title="Mark the currently selected tile as complete" v-on:click="mark_tile">Mark Tile</div>
			<div class="" title="Mark the entire screen as complete" v-on:click="mark_screen">Mark Screen</div>

			<div class="divider"></div>

			<div v-for="image in images" v-bind:class="{selected: image.url == selected_image.url}" v-on:click="load_image(image.url)">{{image.label}}</div>

			<div class="divider"></div>

			<div v-for="zoom_level in zoom_levels" v-bind:class="{selected: zoom_level == zoom}" v-on:click="on_change_zoom(zoom_level)">{{(zoom_level * 100)}}%</div>
		</div>

		<div ref="konva_stage" class="konva_stage">
			<v-stage ref="stage" :config="konva_stage" v-bind:style="{width:selected_image.width+'px', height:selected_image.height+'px'}"></v-stage>
		</div>

		<div class="help" v-if="show_help" v-on:click="show_help = false">
			<div class="help_container">
				<h4>Help / Comments</h4>

				<p class="intro">Early testing made it clear that a fixed tile size was the better approach to calculate tile dimensions <span class="note">(a forumla splits the image into a 10 x 10 grid)</span>. Then, to allow for a more flexible and natural tagging interface, the user can scroll to reposition the viewport and draw outside of the current tile <span class="note">(example: red blob in the 3rd and 4th panel overlaps multiple tiles)</span>.</p>
				<p class="intro">To avoid confusion when the current tile is outside <span class="note">(or larger)</span> than the viewport, a <strong>Mark Screen</strong> button was introduced which marks anything the user sees <span class="note">(does not matter if it is inside the current tile or not)</span>. To test both options, I included both the <strong>Mark Tile</strong> and <strong>Mark Screen</strong> buttons <span class="note">(recommend including only one in the final interface to avoid confusion)</span>.</p>
				<p class="intro">Overall progress will be calculated by pixels <span class="note">(eventually would also summarize all of the tiles together)</span>.</p>

				<hr />

				<h4>Tips</h4>

				<ul class="disc">
					<li>Draw on the image to mark objects <span class="note green">(in green)</span>.</li>
					<li>Click on the buttons to <strong>Mark Tile</strong> or <strong>Mark Screen</strong> <span class="note black">(in black)</span></li>
					<li>The <strong>cells</strong> and <strong>snow</strong> buttons switch between images.</li>
					<li>Use the mousewheel to scroll the viewport.</li>
					<li>Clicking on the navigator panel switches the selected tile <span class="note">(no dragging the viewport yet…grey outline)</span>.</li>
					<li><span class="note">KEYBOARD:</span> <strong>Arrows</strong> to change selected tile <span class="note yellow">(in yellow border)</span></li>
					<li><span class="note">KEYBOARD:</span> <strong>Spacebar</strong> will center on the selected tile</li>
					<li><span class="note">KEYBOARD:</span> <strong>Enter</strong> toggles zoom in/out on selected tile</li>
					<li><strong>Double click</strong> on image to select a tile. <span class="note">KEYBOARD:</span> <strong>Shift-Double click</strong> to also zoom in on that tile</li>
					<li>Tile stats above the navigator panel show % of tagged pixels for the selected tile<br /><span class="note">NOTE: There is a small bug where some of the zoomed in views don't calculate properly.</span></li>
					<li>The 0,0 and 2400,2400 text in the upper left and lower right cornerns are temporary placeholders embedded into the image and will NOT be there in the final version.</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

import Artyom from "artyom.js";
import EventBus from '../../event_bus/event_bus.js';

export default {
	name: 'prototype_tile',
	components: {
	},
	props: {
	},
	computed: {
	},
	data() {
		return {
			selected_image: {
				url: null,

				image_width: 0, //original image width (before zoom)
				image_height: 0,

				width: 0, //set once the image has been loaded
				height: 0,
			},
			images: [
				{url: '/static/images/prototype_tile.jpg', label: 'cells'},
				{url: '/static/images/prototype_tile_snow.jpg', label: 'snow'},
			],

			zoom: 1, //16, //based on 1 = 100%, 16 = 1600%
			zoom_levels: [1,2,4,8,16],

			show_help: true,

			konva_stage: {
				width: 600, //will be set to viewport dimensions
				height: 600
			},

			//all navigator and screen position formulas are based on this as it is the larger set of data
			scroll: {
				left: 0,
				top: 0,

				max: {
					left: 0,
					top: 0
				}
			},

			tile: {
				obj: null,

				x: 0,
				y: 0,
				width: 0,
				height: 0,

				total_pixel_count: 0, //number of pixels in the selected tile
				complete_pixel_count: 0, //number of completed pixels in the selected tile

				count: 10, //number of tiles in row and column (make same ratio as image)
			},

			//duplicated here to avoid vueJS nexttick bug. should really only be defined once
			navigator_konva_stage: {
				width: 200, //will be set to viewport dimensions
				height: 200
			},
			navigator: {
				stage: null,
				image_layer: null, //stores the image
				viewport_layer: null, //shows the amount of image in the main viewport
				hover_layer: null, //mouseover hover indicator
				tile_layer: null, //currently selected tile

				grid: {
					width: 200 / 10, //change to calculated in the future (based on navigator_konva_stage.width / tile.count)
					height: 200 / 10
				},

				tile: {
					obj: null
				},

				hover: {
					obj: null
				},

				viewport: {
					obj: null
				}
			},

			stage: null,
			image_layer: null,
			draw_layer: null,
			grid_layer: null,
			tile_layer: null,

//			draw_context: null,
			is_drawing: false,
			last_pointer_position: null,

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		mark_tile(e) {
			let rect = new Konva.Rect({
				x: (this.tile.x * this.tile.width) / this.zoom,
				y: (this.tile.y * this.tile.height) / this.zoom,
				width: this.tile.width / this.zoom,
				height: this.tile.height / this.zoom,

				fill: '#000', //'#5f5'
				listening: false
			});
			this.draw_layer.add(rect);
			this.draw_layer.draw();

			this.update_tile_stats();
		},

		//********************************************
		mark_screen(e) {
			let rect = new Konva.Rect({
				x: this.scroll.left / this.zoom,
				y: this.scroll.top / this.zoom,
				width: this.viewport.clientWidth / this.zoom,
				height: this.viewport.clientHeight / this.zoom,

				fill: '#000', //'#5f5'
				listening: false
			});
			this.draw_layer.add(rect);
			this.draw_layer.draw();

			this.update_tile_stats();
		},

		//********************************************
		on_navigation_click(e) {
			e = e.evt; //focus just on the mouse event

			//click inside of tile, so offset click location to upper left of the location
			let click = {
				x: Math.floor(e.offsetX / this.navigator.grid.width), // * this.navigator.grid.width,
				y: Math.floor(e.offsetY / this.navigator.grid.height) // * this.navigator.grid.height
			};

			//select this tile
			this.select_tile({
				x: click.x,
				y: click.y,
				centered: true
			});
		},

		//********************************************
		//position is percentage % 0-1
		scroll_to(position) {
			if (position.left < 0) {position.left = 0;}
			if (position.left > 1) {position.left = 1;}
			if (position.top < 0) {position.top = 0;}
			if (position.top > 1) {position.top = 1;}

			//should really save this info instead of looking it up each time (on image load and resize)
			this.$refs['konva_stage'].scrollLeft = position.left * this.scroll.max.left; // this.$refs['konva_stage'].scrollWidth;
			this.$refs['konva_stage'].scrollTop = position.top * this.scroll.max.top; // this.$refs['konva_stage'].scrollHeight;

			this.on_scroll();
		},

		//********************************************
		on_change_zoom(level) {
			this.zoom = level;

//something is broken in the update function. for now, just reset the scroll to 0
			this.$refs['konva_stage'].scrollLeft = 0;
			this.$refs['konva_stage'].scrollTop = 0;

			this.scroll.left = this.$refs['konva_stage'].scrollLeft;
			this.scroll.top = this.$refs['konva_stage'].scrollTop;

			this.navigator.viewport_layer.position({
				x: 0,
				y: 0
			});

			this.stage.container().style.transform = `translate(${this.scroll.left}px, ${this.scroll.top}px)`;

			//counter stage position
			this.stage.x(-this.scroll.left);
			this.stage.y(-this.scroll.top);

			this.update();

			this.select_tile({
				x: this.tile.x,
				y: this.tile.y,
				centered: true
			});
		},

		//********************************************
		load_image(url) {
			Konva.Image.fromURL(url, (img)=>{
				this.selected_image.image_width = img.getAttr('image').width;
				this.selected_image.image_height = img.getAttr('image').height;
				this.selected_image.url = url;

				img.setAttrs({
					x: 0,
					y: 0
				});

				this.image_layer.clear();

				this.image_layer.imageSmoothingEnabled(false); //show pixels when zoomed in
				this.image_layer.add(img);

				this.navigator.image_layer.clear();
				this.navigator.image_layer.add(img.clone());
				this.navigator.image_layer.scale({
					x: this.navigator_konva_stage.width / this.selected_image.image_width, //eventually need to adjust navigator dimensions based on non-square images
					y: this.navigator_konva_stage.height / this.selected_image.image_height
				});
				this.navigator.image_layer.draw();
				this.navigator.image_layer.cache();

				this.update();
			});
		},

		//********************************************
		on_navigation_hover_start(e) {
			//add hover object
			this.navigator.hover_layer.clear();
			if (this.navigator.hover.obj) {this.navigator.hover.obj.destroy();} //should not need this

			this.navigator.hover.obj = new Konva.Rect({ //no opacity since that appears to interfere with the mouseleave event
				x: 0,
				y: 0,
				width: this.navigator.grid.width,
				height: this.navigator.grid.height,
				stroke: '#fff',
				strokeWidth: 2,
				listening: false //otherwise the mouseleave would not fire
			});
			this.navigator.hover_layer.add(this.navigator.hover.obj);
//			this.navigator.hover_layer.show();

			this.navigator.stage.draw();

this.navigator.stage.off('mousemove', this.on_navigation_hover_move); //should not need this, added since the on_navigation_hover_end is not firing
			this.navigator.stage.on('mousemove', this.on_navigation_hover_move);
		},

		//********************************************
		on_navigation_hover_end(e) {
//			this.navigator.hover_layer.hide();
//should not need to destroy each time, but here to avoid listening bug
//NOTE: believe this is now fixed...but could use testing before switching back to show/hide structure
			this.navigator.hover.obj.destroy();
			this.navigator.stage.draw();

			this.navigator.stage.off('mousemove', this.on_navigation_hover_move);
		},

		//********************************************
		on_navigation_hover_move(e) {
			e = e.evt; //focus just on the mouse event

			let hover = {
				x: Math.floor(e.offsetX / this.navigator.grid.width),
				y: Math.floor(e.offsetY / this.navigator.grid.height),
			};

			this.navigator.hover_layer.position({
				x: hover.x * this.navigator.grid.width,
				y: hover.y * this.navigator.grid.height
			});

			this.navigator.stage.draw();
		},

		//********************************************
		on_scroll(e) {
			this.scroll.left = this.$refs['konva_stage'].scrollLeft;
			this.scroll.top = this.$refs['konva_stage'].scrollTop;

			//transform stage container
			this.stage.container().style.transform = `translate(${this.scroll.left}px, ${this.scroll.top}px)`;

			//counter stage position
			this.stage.x(-this.scroll.left);
			this.stage.y(-this.scroll.top);

			this.stage.draw();

			//convert scroll position to navigator position
			this.navigator.viewport_layer.position({
				x: this.scroll.left * (this.navigator_konva_stage.width - this.navigator.viewport.obj.width()) / this.scroll.max.left,
				y: this.scroll.top * (this.navigator_konva_stage.height - this.navigator.viewport.obj.height()) / this.scroll.max.top
			});

			this.navigator.stage.draw();
		},

		//********************************************
		update() {
			//image update
			this.selected_image.width = (this.selected_image.image_width * this.zoom);
			this.selected_image.height = (this.selected_image.image_height * this.zoom);

			//update the size of the stage container
			this.stage.container().style.width = this.selected_image.width + 'px';
			this.stage.container().style.height = this.selected_image.height + 'px';

			//image
			if (this.selected_image.width > 0 && this.selected_image.height > 0) {
				this.image_layer.scale({
					x: this.zoom,
					y: this.zoom
				});

				this.image_layer.draw();
				this.image_layer.cache(); //also helps create pixel render
			}

//			this.draw_context.scale(this.zoom, this.zoom);
			this.draw_layer.scale({
				x: this.zoom,
				y: this.zoom
			});
			this.draw_layer.draw();

			this.stage.draw();

			//now update items that needed the stage to be rendered

			//viewport dimensions
			this.scroll.max.left = this.selected_image.width - this.viewport.clientWidth;
			this.scroll.max.top = this.selected_image.height - this.viewport.clientHeight;
//			this.scroll.max.left = this.viewport.scrollWidth - this.viewport.offsetWidth; //this.viewport.clientWidth;
//			this.scroll.max.top = this.viewport.scrollHeight - this.viewport.offsetHeight; //this.viewport.clientHeight;

			//navigator
			if (this.navigator.tile.obj) {
				let viewport_width = this.viewport.clientWidth;
				let viewport_height = this.viewport.clientHeight; //this.$refs['prototype_tile'].clientHeight;

				this.navigator.tile.obj.size({
					width: this.navigator.grid.width,
					height: this.navigator.grid.height,
				});

				this.navigator.tile_layer.position({
					x: (this.tile.x * this.navigator.grid.width),
					y: (this.tile.y * this.navigator.grid.height)
				});

				//viewport
				if (this.selected_image.width && this.selected_image.height) {
					this.navigator.viewport.obj.size({
						width: viewport_width * this.navigator_konva_stage.width / this.selected_image.width,
						height: viewport_height * this.navigator_konva_stage.height / this.selected_image.height
					});
				}

				this.navigator.stage.draw();
			}

			//draw grid (never scale to keep thin lines)
//really shouldn't need to destroy this layer each time, but doing this to avoid display bug
			if (this.grid_layer) {this.grid_layer.destroy();}
			this.grid_layer = new Konva.Layer({
				opacity: .2,
				listening: false
			});
			this.stage.add(this.grid_layer);

			let grid_size = this.selected_image.width / this.tile.count;

			for (var i=1; i<this.tile.count; i++) {
				let x = (i * grid_size);
				if (x > this.selected_image.width) {break;}

				this.grid_layer.add(new Konva.Line({
					stroke: '#fff',
					points: [x - .5, 0, x - .5, this.selected_image.height]
				}));
				this.grid_layer.add(new Konva.Line({
					stroke: '#000',
					points: [x + .5, 0, x + .5, this.selected_image.height]
				}));
			}

			for (var i=1; i<this.tile.count; i++) {
				let y = (i * grid_size);
				if (y > this.selected_image.height) {break;}

				this.grid_layer.add(new Konva.Line({
					stroke: '#fff',
					points: [0, y - .5, this.selected_image.width, y - .5]
				}));
				this.grid_layer.add(new Konva.Line({
					stroke: '#000',
					points: [0, y + .5, this.selected_image.width, y + .5]
				}));
			}

			//tile
			this.tile.width = (this.selected_image.width / this.tile.count);
			this.tile.height = (this.selected_image.height / this.tile.count);

			this.tile.obj.size({
				width: this.tile.width + 3, //offset for border
				height: this.tile.height + 3
			});
			this.tile.obj.position({
				x: (this.tile.x * this.tile.width) - (3/2),
				y: (this.tile.y * this.tile.height) - (3/2)
			});

			this.stage.draw();
		},

		//********************************************
		on_double_click(e) {
			e = e.evt; //focus just on the mouse event

			var position = {
				x: Math.floor((e.offsetX + this.scroll.left) / this.tile.width),
				y: Math.floor((e.offsetY + this.scroll.top) / this.tile.width)
			};

			this.select_tile({
				x: position.x,
				y: position.y
			});

			//zoom in as well (if shift key was pressed)
			let is_shift_pressed = (e.shiftKey) ? e.shiftKey : false;

			if (is_shift_pressed) {this.on_change_zoom(4);} //(this.zoom > 1) ? 1 :
		},

		//********************************************
		start_drawing(e) {
			this.is_drawing = true;

			this.last_pointer_position = this.stage.getPointerPosition();
		},

		//********************************************
		stop_drawing(e) {
			this.is_drawing = false;

			var position = this.stage.getPointerPosition();

			this.update_tile_stats();
		},

		//********************************************
		on_move_drawing(e) {
			let position = this.stage.getPointerPosition();

			//if not drawing, then skip the rest of this function
			if (!this.is_drawing) {return;}

//			this.draw_context.beginPath();

			let local_position = {
				x: (this.last_pointer_position.x + this.scroll.left) / this.zoom, // - this.image_layer.x()
				y: (this.last_pointer_position.y + this.scroll.top) / this.zoom // - this.image_layer.y()
			};

//			this.draw_context.moveTo(local_position.x, local_position.y);

			let new_position = {
				x: (position.x + this.scroll.left) / this.zoom, // - this.image_layer.x()
				y: (position.y + this.scroll.top) / this.zoom // - this.image_layer.y()
			};

			let line = new Konva.Line({
				points: [local_position.x, local_position.y, new_position.x, new_position.y],
				stroke: '#5f5',
				strokeWidth: 15 - Math.floor(this.zoom / 1.1),
				lineJoin: 'round',
				lineCap: 'round',
				listening: false
			});
			this.draw_layer.add(line);
			this.draw_layer.draw();
//			this.draw_layer.cache(); //also helps create pixel render

/*
			this.draw_context.strokeStyle = '#5f5';
			this.draw_context.lineJoin = 'round';
			this.draw_context.lineWidth = 15;
			this.draw_context.lineTo(new_position.x, new_position.y);
			this.draw_context.closePath();
			this.draw_context.stroke();
*/
			this.last_pointer_position = position;
		},

		//********************************************
		on_resize() {
			//change stage size to match viewport
			this.stage.width(this.viewport.clientWidth);
			this.stage.height(this.viewport.clientHeight);

			this.update();
		},

		//********************************************
		select_tile(parameters) {
			parameters.x = (typeof parameters.x != 'undefined') ? parameters.x : 0;
			parameters.y = (typeof parameters.y != 'undefined') ? parameters.y : 0;
			parameters.centered = (typeof parameters.centered != 'undefined') ? parameters.centered : false;

			//contrain to grid
			if (parameters.x < 0) {parameters.x = 0;}
			if (parameters.x > this.tile.count - 1) {parameters.x = this.tile.count - 1;}
			if (parameters.y < 0) {parameters.y = 0;}
			if (parameters.y > this.tile.count - 1) {parameters.y = this.tile.count - 1;}

			//tile
			this.tile.x = parameters.x;
			this.tile.y = parameters.y;

			this.tile.obj.position({
				x: (this.tile.x * this.tile.width) - (3/2),
				y: (this.tile.y * this.tile.height) - (3/2)
			});

			this.tile_layer.draw();

			//navigator
			this.navigator.tile_layer.position({
				x: (this.tile.x * this.navigator.grid.width),
				y: (this.tile.y * this.navigator.grid.height)
			});
			this.navigator.stage.draw();

			this.update_tile_stats();

			//do we need to center the main viewport
			let viewport_width = this.viewport.clientWidth;
			let viewport_height = this.viewport.clientHeight;

//console.error(this.tile.x + ' x ' + this.tile.y + ' :: ' + (this.tile.x * this.navigator.grid.width) + ' x ' + (this.tile.y * this.navigator.grid.height));

			//covert everything to a percentage
			let navigator_viewport = {
				x: {
					min: (this.tile.x * this.navigator.grid.width) / this.navigator_konva_stage.width,
					max: ((this.tile.x * this.navigator.grid.width) + this.navigator.grid.width) / this.navigator_konva_stage.width,
				},
				y: {
					min: (this.tile.y * this.navigator.grid.height) / this.navigator_konva_stage.height,
					max: ((this.tile.y * this.navigator.grid.height) + this.navigator.grid.height) / this.navigator_konva_stage.height,
				},
			};

//console.log(navigator_viewport);

			let scroll = {
				left: this.$refs['konva_stage'].scrollLeft, // * this.viewport.scrollWidth / this.scroll.max.left,
				top: this.$refs['konva_stage'].scrollTop // * this.viewport.scrollHeight / this.scroll.max.top,
			};

			let screen = {
				x: {
					min: scroll.left / this.selected_image.width, //this.viewport.scrollWidth, //this.scroll.max.left,
					max: (scroll.left + viewport_width) / this.selected_image.width //this.viewport.scrollWidth, //this.scroll.max.left
				},
				y: {
					min: scroll.top / this.selected_image.height, //this.viewport.scrollHeight, //(this.scroll.max.top + viewport_height),
					max: (scroll.top + viewport_height) / this.selected_image.height //this.viewport.scrollHeight, //(this.scroll.max.left + viewport_height)
				}
			};

//console.log(screen);

			let on_screen_horizontally = (navigator_viewport.x.min >= screen.x.min && navigator_viewport.x.max <= screen.x.max) ? true : false;
			let on_screen_vertically = (navigator_viewport.y.min >= screen.y.min && navigator_viewport.y.max <= screen.y.max) ? true : false;
			let on_screen = (on_screen_horizontally && on_screen_vertically) ? true: false;

//console.error('on screen: ' + on_screen);

			if (!on_screen) {
				//if screen size is smaller than tile, force centered
				if (this.tile.width >= viewport_width || this.tile.height >= viewport_height) {parameters.centered = true;}

				//convert current scroll position to percentage
				let position = {
					left: scroll.left / this.scroll.max.left,
					top: scroll.top / this.scroll.max.top,
				};

				//update the scroll only if needed
				if (!on_screen_horizontally || parameters.centered) {
					//center
					if (parameters.centered) {
						let left = (((navigator_viewport.x.min + navigator_viewport.x.max) / 2) - ((screen.x.max - screen.x.min) / 2));
						position.left = left * this.selected_image.width / this.scroll.max.left; //convert to max scroll percentages (from selected_image.width percentage)
					} else {
						let move_amount = this.tile.width / this.scroll.max.left; //amount of percentage to move
						position.left = (navigator_viewport.x.min < screen.x.min) ? position.left - move_amount : position.left + move_amount;
					}
				}
				if (!on_screen_vertically || parameters.centered) {
					//center
					if (parameters.centered) {
						let top = (((navigator_viewport.y.min + navigator_viewport.y.max) / 2) - ((screen.y.max - screen.y.min) / 2));
						position.top = top * this.selected_image.height / this.scroll.max.top; //convert to max scroll percentages (from selected_image.width percentage)
					} else {
						let move_amount = this.tile.height / this.scroll.max.top; //amount of percentage to move
						position.top = (navigator_viewport.y.min < screen.y.min) ? position.top - move_amount : position.top + move_amount;
					}
				}

				//convert back to min/max scroll
				this.scroll_to(position);
			}
		},

		//********************************************
		update_tile_stats() {
			if (this.tile.width == 0 || this.tile.height == 0) {return;}

			//too large of a data set when zoomed in (ok if only comparing at zoome level 1...might look into keeping the draw layer smaller?)
/*
			if (this.zoom > 2) {
				this.tile.total_pixel_count = 0;
				this.tile.complete_pixel_count = 0;

				return;
			}
*/

			let selected_tile = {
				x: ((this.tile.x * this.tile.width)) / this.zoom, // / this.zoom,  - this.scroll.left
				y: ((this.tile.y * this.tile.height)) / this.zoom, // / this.zoom,  - this.scroll.top
				width: this.tile.width / this.zoom, // / this.zoom,
				height: this.tile.height / this.zoom // / this.zoom
			};

			//switch to zoom level 1 to gather stats (smaller bitmap to scan)
			this.draw_layer.scale({
				x: 1,
				y: 1
			});
			this.draw_layer.draw();

			let context = this.draw_layer.getContext();
			let image_data = context.getImageData(selected_tile.x, selected_tile.y, selected_tile.width, selected_tile.height);

			//switch back to current zoom
			this.draw_layer.scale({
				x: this.zoom,
				y: this.zoom
			});
			this.draw_layer.draw();

			//reduce down to alpha pixels
			let alpha_pixels = image_data.data.filter((value, index) => {
				return index % 4 === 3;
			});
			let complete_pixels = alpha_pixels.filter((value)=>value > 0);

			this.tile.total_pixel_count = alpha_pixels.length; //image_data.data.length / 4;
			this.tile.complete_pixel_count = complete_pixels.length;
		},

		//********************************************
		on_keydown(e) {
			switch(e.keyCode) {
				case 13:
					//enter
					this.on_change_zoom((this.zoom > 1) ? 1 : 4);
					e.preventDefault();
					break;
				case 32:
					//spacebar
					this.select_tile({
						x: this.tile.x,
						y: this.tile.y,
						centered: true
					});
					e.preventDefault();
					break;
				case 37:
					//left
					this.select_tile({
						x: this.tile.x - 1,
						y: this.tile.y
					});
					e.preventDefault();
					break;
				case 38:
					//up
					this.select_tile({
						x: this.tile.x,
						y: this.tile.y - 1
					});
					e.preventDefault();
					break;
				case 39:
					//right
					this.select_tile({
						x: this.tile.x + 1,
						y: this.tile.y
					});
					e.preventDefault();
					break;
				case 40:
					//down
					this.select_tile({
						x: this.tile.x,
						y: this.tile.y + 1
					});
					e.preventDefault();
					break;
			}
		},

		//********************************************
		on_keyup(e) {
		}
	},
	destroyed() {
		if (this.$refs['konva_stage']) {this.$refs['konva_stage'].removeEventListener('scroll', this.on_scroll);}

		window.removeEventListener('resize', this.on_resize);

		window.removeEventListener('keydown', this.on_keydown);
		window.removeEventListener('keyup', this.on_keyup);
	},
	mounted() {
		this.viewport = this.$refs['konva_stage'];
		this.stage = this.$refs['stage'].getStage();

		// add the layer to the stage
		this.image_layer = new Konva.Layer();
		this.stage.add(this.image_layer);

		this.draw_layer = new Konva.FastLayer({ //using fast layer to avoid
//			clearBeforeDraw: false,
			listening: false
		});
		this.draw_layer.imageSmoothingEnabled(false); //show pixels when zoomed in
		this.stage.add(this.draw_layer);

//		this.draw_context = this.draw_layer.getContext();

		this.tile_layer = new Konva.Layer();
		this.stage.add(this.tile_layer);

		this.tile.obj = new Konva.Rect({
			x: this.tile.x - (3/2),
			y: this.tile.y - (3/2),
			width: this.tile.width,
			height: this.tile.height,
			stroke: '#faef5c', //yellow
			strokeWidth: 3,
			listening: false
		});
		this.tile.obj.opacity(.8);
		this.tile_layer.add(this.tile.obj);

		//navigator
		this.navigator.stage = this.$refs['navigator_konva_stage'].getStage();
		this.navigator.stage.on('mouseenter', this.on_navigation_hover_start);
		this.navigator.stage.on('mouseleave', this.on_navigation_hover_end);
		this.navigator.stage.on('click', this.on_navigation_click);

		//image
		this.navigator.image_layer = new Konva.Layer({
			listening: false
		});
		this.navigator.stage.add(this.navigator.image_layer);

		//viewport
		this.navigator.viewport_layer = new Konva.Layer({
			listening: false
		});
		this.navigator.stage.add(this.navigator.viewport_layer);
		this.navigator.viewport.obj = new Konva.Rect({
			x: 0,
			y: 0,
			width: 10,
			height: 10,
			stroke: '#777',
			strokeWidth: 2,
			opacity: .85
		});
		this.navigator.viewport_layer.add(this.navigator.viewport.obj);

		//hover
		this.navigator.hover_layer = new Konva.Layer();
		this.navigator.stage.add(this.navigator.hover_layer);

		//tile
		this.navigator.tile_layer = new Konva.Layer({
			listening: false
		});
		this.navigator.stage.add(this.navigator.tile_layer);
		this.navigator.tile.obj = new Konva.Rect({
			x: 0,
			y: 0,
			width: 10,
			height: 10,
			stroke: '#faef5c', //yellow
			strokeWidth: 2
		});
		this.navigator.tile_layer.add(this.navigator.tile.obj);

		//load image
		this.load_image(this.images[0].url);

		//events
		this.$refs['konva_stage'].addEventListener('scroll', this.on_scroll);

		this.image_layer.on('mousedown touchstart', this.start_drawing);
		this.image_layer.on('mouseup touchend', this.stop_drawing);
		this.image_layer.on('mousemove touchmove', this.on_move_drawing);

		this.image_layer.on('dblclick', this.on_double_click);

		//keyboard events
		window.addEventListener('keydown', this.on_keydown);
		window.addEventListener('keyup', this.on_keyup);

		//reseize event
		this.on_resize();
		window.addEventListener('resize', this.on_resize); //add timer into this to gain better performance on resize (instead of every pixel resized)
	},
	watch: {
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


