<template>
	<div class="prototype_phylogeny">
		<div class="header">
			<input type="text" size="30" placeholder="Search" />
		</div>

		<div class="prototype_phylogeny_diagram">
			<div class="flexbox_concept"
				v-bind:class="{has_selected_block: has_selected_block, navigation_mode: (global.phylogeny_show_table && has_selected_block)}"
			>
				<div class="subclone_label">{{phylogeny_proportion_id}}</div>

				<prototype_phylogeny_block v-for="(data,index) in cell_data"
					v-bind:json_data="data"

					v-bind:sample="(data.sample) ? data.sample : 'primary'"
					v-bind:label="data.label"
					v-bind:color="data.color"
					v-bind:genes="(data.genes) ? data.genes : 0"
					v-bind:show_end_block="(data.show_end_block) ? data.show_end_block : false"

					v-bind:children="data.children"

					v-bind:key="index"
				></prototype_phylogeny_block>
			</div>

<!--
v-if="proportion_index != global.phylogeny_proportion_id"
v-bind:class="{selected: proportion_index == global.phylogeny_proportion_id}"
 -->
			<div class="flexbox_concept is_narrow"
				v-for="(proportion, proportion_index) in global.phylogeny_proportion_sets"

				v-bind:class="{selected: proportion_index == global.phylogeny_proportion_id, navigation_mode: (global.phylogeny_show_table && has_selected_block)}"
				v-on:click.stop="select_proportion(proportion_index)"
			>
				<div class="subclone_label">{{proportion_index}}</div>

				<div class="prototype_phylogeny_block cell_block">
					<div class="children">
						<prototype_phylogeny_block v-for="(data,index) in proportion"
							v-bind:show_primary_blocks="true"

							v-bind:block_label="data.proportion + '%'"

							v-bind:color="data.color"
							v-bind:show_stripes="false"
							v-bind:parent_total="data.proportion"
							v-bind:show_end_block="false"

							v-bind:show_stats="true"
							v-bind:ignore_scaling="true"

							v-bind:key="index"
							v-bind:on_click="select_proportion"
							v-bind:callback_value="proportion_index"
						></prototype_phylogeny_block>
					</div>
				</div>
			</div>
		</div>

		<prototype_phylogeny_table v-if="has_selected_block && global.phylogeny_show_table"></prototype_phylogeny_table>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

import prototype_phylogeny_block from './prototype_phylogeny_block';
import prototype_phylogeny_table from './prototype_phylogeny_table';

import EventBus from '../../event_bus/event_bus.js';

import phylogeny_json_data from './phylogeny_aml_tree.json';


export default {
	name: 'prototype_phylogeny',
	components: {
		prototype_phylogeny_block,
		prototype_phylogeny_table
	},
	props: {
	},
	computed: {
		phylogeny_proportion_id: function() {
			this.global.phylogeny_relapse_data;

			return this.global.phylogeny_proportion_id;
		},
		relapse_data: function() {
			this.global.phylogeny_relapse_data;

			return Object.values(this.global.phylogeny_relapse_data);
		},
		has_selected_block: function() {
			//reference this variable so this computed value will update when this variable does
			//https://logaretm.com/blog/2019-10-11-forcing-recomputation-of-computed-properties
			this.global.selected_blocks;

			return (this.global.selected_blocks.length > 0) ? true : false;
		},
	},
	data() {
		return {
			phylogeny_data: phylogeny_json_data,
//			data_string: JS ON.st ringify(data), //used to convert charles data into valid json data

			cell_data: [],

			//global data
			global: this.$root._data
		}
	},
	methods: {
/*
		//********************************************
		update_gene_total() {
			let total = 0;
			let calculate = null;

			calculate = function(data) {
				return (data.t1_mutations) ? data.t1_mutations : 0;
			};

			calculate(this.cell_data);

//console.log(this.cell_data);
		},
*/
/*
		//********************************************
		on_select_relapse(obj) {
			//globally change the indicate which blocks are selected (or none at all)
			if (this.global.selected_blocks && this.global.selected_blocks[0] == obj) {
				//deselect this block
				this.global.selected_blocks = [];
			} else {
				//walk through and find any parent blocks for this selected block
				let selected_blocks = [];

				//make sure this is added (and first in the array)
				selected_blocks.push(obj);

				//recursively walk through parents
				for (let i=0; i<obj.block_list.length; i++) {
					selected_blocks.push(obj.block_list[i].parent);

					selected_blocks = selected_blocks.concat(obj.block_list[i].parent.get_all_parents());
				}

				this.global.selected_blocks = selected_blocks;
			}
		},

		//********************************************
		get_all_relapse_data() {
			let data = [];

			for (let index in this.global.phylogeny_relapse_data) {
				data = data.concat(this.global.phylogeny_relapse_data[index].block_list);
			}

			return data;
		},
*/
		//********************************************
//convert json structure to new structure (ok to delete once we have the new data set)
		convert_data(data) {
			for (var i=0; i<data.length; i++) {
				let proportion = Number(data[i].proportion);

				//create new structure
				data[i].proportion = {
					'primary': proportion,
					'relapse': 0
				};

				//children
				if (data[i].children && data[i].children.length > 0) {
					data[i].children = this.convert_data(data[i].children);
				}
			}

			return data;
		},

		//********************************************
		select_proportion(proportion) {
			this.global.phylogeny_proportion_id = proportion;

			//deselect block
			this.global.selected_blocks = [];
		}
	},
	destroyed() {
	},
	mounted() {
//		this.update_gene_total();

		//convert from old data structure to new (ok to delete)
//		var cell_data = this.convert_data(phylogeny_json_data.cell_data);
//		console.log(JSO N.st ringify({'cell_data':cell_data}, null, 2));

		//only here because the phylogeny_relapse_data has been updated but vueJS didn't know it yet
		this.$forceUpdate();
	},
	watch: {
	},
	created() {
		this.cell_data = this.phylogeny_data['cell_data'];
/*
		//seeing if waiting for the DOM will get rid of the pink background color issue
		Vue.nextTick(()=>{
		});
*/
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


