<template>
	<div class="prototype prototype_histogram_overlay">
		<h1>Histogram Overlay</h1>

		<div class="konva_stage">
			<v-stage ref="stage" :config="konva_stage"></v-stage>
		</div>

		<div class="presets">
			<div class="button" v-on:click="is_limited_by_color = !is_limited_by_color" v-bind:class="[{active:is_limited_by_color}]">Emphasis on selected color</div>
			<div class="button" v-on:click="draw_graph">[re]draw graph</div>
		</div>

		<div class="setting_group">
			<div class="setting" v-for="(setting, index) of settings" v-bind:class="[{active: setting.is_active}]" v-on:mouseover="selected_setting = index" v-on:mouseout="selected_setting = null">
				<div class="label" v-on:click="toggle_setting_info(index)">
					<div v-html="setting.label"></div>
<!--					<div class="description" v-if="setting.description" v-html="setting.description"></div>-->
				</div>

				<circle-slider v-model="setting.value" v-bind="slider_config" class="slider"
					v-bind:min="setting.min"
					v-bind:max="setting.max"
					v-bind:stepSize="setting.step"
					v-bind:progressColor="setting.color"
					v-bind:knobColor="setting.color"
				></circle-slider>

				<div class="progress_bar" v-bind:style="{width: setting.progress_bar + '%'}" v-if="setting.progress_bar"></div>

				<div class="value">
					{{setting.value}}
					<span class="unit" v-if="setting.unit" v-html="setting.unit"></span>
				</div>
			</div>
		</div>

		<div class="description">
			<h2>Comments</h2>

			<ul class="disc">
				<li>soft grey graph indicates the expert range—height of grey bar indicates min/max brightness</li>
				<li>chart is setup as 0% brightness on bottom and 100% brightness at top</li>
				<li>white dots indicate selected color range is within the expert range</li>
				<li>color indicates when the selected colors are OUTSIDE the expert range (either higher or lower)</li>
			</ul>
		</div>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

export default {
	name: 'prototype_panel',
	components: {
	},
	props: {
	},
	computed: {
	},
	data() {
		return {
			//local data
			slider_config: {
				progressColor: "#1dbd80",
				knobColor: "#1dbd80",

				circleWidthRel: 10,
				progressWidthRel: 5,
				knobRadiusRel: 7,
				side: 105
			},

			settings: {
				delay: {
					value: 67,
					label: 'Selected Color',
					description: 'Primary selected hue',
					min: 0,
					max: 255,
					step: 1,
					color: '#e1ff00',
					unit: 'hue',
					is_active: false,

					progress_bar: 0
				}
			},

			color_width: 2,
			is_limited_by_color: true,

			konva_stage: {
				width: 256 * 2, //color_width
				height: 150
			},

			stage: null,
			histogram_layer: null,
			overlay_layer: null,

			timer: null,

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		//other js library to consider  https://gist.github.com/mjackson/5311256
		//https://css-tricks.com/converting-color-spaces-in-javascript/

//FUTURE: look into konvaJS filters for HSL https://konvajs.org/api/Konva.Filters.html#.HSL

		HSLToHex(h, s, l) {
			s /= 100;
			l /= 100;

			let c = (1 - Math.abs(2 * l - 1)) * s,
			x = c * (1 - Math.abs((h / 60) % 2 - 1)),
			m = l - c/2,
			r = 0,
			g = 0,
			b = 0;

			if (0 <= h && h < 60) {
				r = c; g = x; b = 0;
			} else if (60 <= h && h < 120) {
				r = x; g = c; b = 0;
			} else if (120 <= h && h < 180) {
				r = 0; g = c; b = x;
			} else if (180 <= h && h < 240) {
				r = 0; g = x; b = c;
			} else if (240 <= h && h < 300) {
				r = x; g = 0; b = c;
			} else if (300 <= h && h < 360) {
				r = c; g = 0; b = x;
			}

			// Having obtained RGB, convert channels to hex
			r = Math.round((r + m) * 255).toString(16);
			g = Math.round((g + m) * 255).toString(16);
			b = Math.round((b + m) * 255).toString(16);

			// Prepend 0s, if necessary
			if (r.length == 1) {r = "0" + r;}
			if (g.length == 1) {g = "0" + g;}
			if (b.length == 1) {b = "0" + b;}

			return "#" + r + g + b;
		},
		RGBToHSL(r, g, b) {
			// Make r, g, and b fractions of 1
			r /= 255;
			g /= 255;
			b /= 255;

			// Find greatest and smallest channel values
			let cmin = Math.min(r,g,b),
			cmax = Math.max(r,g,b),
			delta = cmax - cmin,
			h = 0,
			s = 0,
			l = 0;

			// Calculate hue
			// No difference
			if (delta == 0)
			h = 0;
			// Red is max
			else if (cmax == r)
			h = ((g - b) / delta) % 6;
			// Green is max
			else if (cmax == g)
			h = (b - r) / delta + 2;
			// Blue is max
			else
			h = (r - g) / delta + 4;

			h = Math.round(h * 60);

			// Make negative hues positive behind 360°
			if (h < 0)
			h += 360;

			// Calculate lightness
			l = (cmax + cmin) / 2;

			// Calculate saturation
			s = delta == 0 ? 0 : delta / (1 - Math.abs(2 * l - 1));

			// Multiply l and s by 100
			s = +(s * 100).toFixed(1);
			l = +(l * 100).toFixed(1);

			return {h:h, s:s, l:l}; //[h,s,l]; //"hsl(" + h + "," + s + "%," + l + "%)";
		},

		//********************************************
		start_timer() {
			this.stop_timer();

			this.timer = setTimeout(()=>{
//console.error('timer done!');
				this.draw_graph();
			}, this.settings['delay'].value);
		},

		//********************************************
		stop_timer() {
			if (this.timer) {
				clearTimeout(this.timer);
				this.timer = null;
			}
		},

		//********************************************
		select_preset(e, parameters) {
			if (typeof parameters['grid_layout'] != 'undefined') {
				this.grid_layout = parameters['grid_layout'];

				Vue.nextTick(()=>{
					this.on_resize();

					for (let index in this.panels) {
//						this.panels[index].stage.draw();
					}
				});
			}
		},

		//********************************************
		draw_graph() {
			//clear layers
			this.histogram_layer.destroyChildren();
			this.overlay_layer.destroyChildren();

			//draw
			let current_height = 10;
			let current_position = 100;

			for (let hue=0; hue<256; hue++) {
				let color = this.HSLToHex(hue, 100, 50);
				let in_hue_range = (hue > this.settings.delay.value - 10 && hue < this.settings.delay.value + 10) ? true : false;

				let height = current_height + Math.floor(Math.random() * 30) - 15;
				if (height < 2) {height = 2;}
				if (height > 30) {height = 30;}

				let position = current_position + Math.floor(Math.random() * 10) - 4.5;
				if (position < 0) {position = 0;}
				if (position > 150 - height) {position = 150 - height;}

				//adjust height when in hue range
				if (in_hue_range) {height *= 2;}

				//flatline position
				if (this.is_limited_by_color) {
					if (in_hue_range) {
						//in selected color, do nothing
						height = Math.floor(Math.random() * 50) + 70;
						position = 150;
					} else {
						position = 150;
						height /= 2;
					}
				}

				current_height = height;
				current_position = position;

				let rect = new Konva.Rect({
					width: this.color_width,
					height: height,
	//				fillEnabled: true,
					fill: '#555', //color,
					x: (this.color_width * hue),
					y: position - height
				});
				rect.fillLinearGradientColorStops(0, '#ffffff', .1, '#333333', .9, '#333333', 1, '#ffffff');
				rect.fillLinearGradientStartPoint({x:0, y:0});
				rect.fillLinearGradientEndPoint({x:0, y:height});
				rect.fillPriority('linear-gradient');

				this.histogram_layer.add(rect);

				//create fake data
				let beyond_position = position;
				let stroke_color = '#ffffff';
				let stroke_width = 2;
				let beyond_y = position - height;

				let is_beyond = (this.is_limited_by_color) ? in_hue_range : (Math.random() > .92);

				if (is_beyond * Math.random() > .5) {
					//beyond
					stroke_color = color;
					stroke_width = Math.floor(Math.random() * 30) + 2;
					beyond_position = (Math.random() > .3) ? beyond_y - (stroke_width / 2): beyond_y + height + (stroke_width / 2);
				} else {
					//inside
//					beyond_position = beyond_y + Math.floor(Math.random() * height);
					beyond_position = beyond_y + (Math.floor(Math.random() * (height / 2)) - (height / 4)) + (height / 2);
				}

				let overlay = new Konva.Line({
					points: [
						(this.color_width * hue), beyond_position,
						(this.color_width * hue) + this.color_width, beyond_position
					],
					strokeWidth: stroke_width,
					stroke: stroke_color
				});
				this.overlay_layer.add(overlay);
			}

			this.histogram_layer.draw();
			this.overlay_layer.draw();
		},

		//********************************************
		on_resize() {
		},
	},
	destroyed() {
		window.removeEventListener('resize', this.on_resize);
	},
	mounted() {
		this.stage = this.$refs['stage'].getStage();

		this.histogram_layer = new Konva.Layer();
		this.stage.add(this.histogram_layer);

		this.overlay_layer = new Konva.Layer();
		this.stage.add(this.overlay_layer);

		this.draw_graph();

		//reseize event
		this.on_resize();
		window.addEventListener('resize', this.on_resize); //add timer into this to gain better performance on resize (instead of every pixel resized)
	},
	watch: {
		is_limited_by_color: function(value, old_value) {
			this.draw_graph();
		},
		'settings.delay.value': function(value, old_value) {
			this.settings.delay.color = this.HSLToHex(value, 100, 50);
//console.log(value + ' :: ' + this.settings.delay.color);

			this.start_timer();
		}
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


