//*********************************************************
//filters

import Vue from 'vue';
import {format} from 'date-fns';

//to allow filters to be used inside components as well
Vue.prototype.$filters = Vue.options.filters;


//*****************
//change datetime to friendly date using date-fns
// {{date|format_date}}
// {{date|format_date('MMM D YYYY')}}
Vue.filter('format_date', function(date, date_format) {
	var date_format = (date_format) ? date_format : 'MMM D';
	return format(date, date_format);
});

//*****************
//convert linebreak to <br /> tag
Vue.filter('nl2br', function(str) {
	return str.replace(/(\r\n|\n\r|\r|\n)/g, '<br />');
});

//*****************
//add <span class="nowrap"> to the last 2 words at the end of the text
Vue.filter('nowrap', function(str) {
	var words = str.split(' ');

	if (words.length > 2) {
		words[words.length - 2] = '<span class="nowrap">' + words[words.length - 2];
		words[words.length - 1] = words[words.length - 1] + '</span>';
	}

	return words.join(' ');
});

//*****************
//convert hex color to r,g,b
//https://stackoverflow.com/questions/5623838/rgb-to-hex-and-hex-to-rgb
Vue.filter('hex2rgb', function(hex) {
	// Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF")
	var shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
	hex = hex.replace(shorthandRegex, function(m, r, g, b) {
		return r + r + g + g + b + b;
	});

	var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
	return result ? {
		r: parseInt(result[1], 16),
		g: parseInt(result[2], 16),
		b: parseInt(result[3], 16)
	} : null;
});


