<template>
	<div class="prototype_phylogeny_table">
		<div class="title">
			<div class="close_btn" v-on:click.stop="close_table"></div>

			<div class="label">Selected: </div>

			<div class="data_label">{{label}}</div>
			<div class="edge_label" v-if="edge_label">{{edge_label}}</div>
			<div class="tier_label" v-on:click.stop="deselect_tier">
				<template v-if="global.phylogeny_selected_tier">
					{{global.phylogeny_selected_tier}} <div class="close_icon"></div>
				</template>
				<template v-if="!global.phylogeny_selected_tier">
					All Tiers
				</template>
			</div>
		</div>

		<table>
			<thead>
				<tr>
					<th v-for="(column,index) in table_columns" v-bind:class="['column_' + index]">
						{{column}}
					</th>
				</tr>
			</thead>

			<tbody>
				<tr v-for="(data) in details_data" v-on:click.stop="select_row(data)" v-if="!global.phylogeny_selected_tier || global.phylogeny_selected_tier == data.tier">
					<td v-for="(column,index) in table_columns" v-bind:class="['column_' + index]">
						{{format_data(data, index)}}
					</td>
				</tr>

				<tr v-if="details_data.length == 0">
					<td v-bind:colspan="Object.count(table_columns)">
						...
					</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

import EventBus from '../../event_bus/event_bus.js';

export default {
	name: 'prototype_phylogeny_table',
	components: {
	},
	props: {
	},
	computed: {
		label: function() {
			let data = this.global.selected_blocks[0];
			let label = (data && data._props && data._props.json_data && data._props.json_data.label) ? data._props.json_data.label : '';

			return label;
		},
		edge_label: function() {
			let data = this.global.selected_blocks[0];
			let label = (data && data._props && data._props.json_data && data._props.json_data.edge_label) ? data._props.json_data.edge_label : '';

			return label;
		},
		details_data: function() {
			let data = this.global.selected_blocks[0];

			return (data && data._props && data._props.json_data && data._props.json_data.details) ? data._props.json_data.details : [];
		}
	},
	data() {
		return {
			selected_tier: null,

			table_columns: {
			  "tier": "Tier",
			  "type": "Type",
			  "trv_type": "TRV Type",
			  "chr": "chr",
			  "start": "Start",
			  "gene": "Gene",
			  "amino_acid_change": "Amino Acid Change",
			  "reference": "Reference",
			  "variant": "Variant",
			  "strand": "Strand",
			  "ucsc_cons": "UCSC Cons",
			  "NORMAL_ALLDNA_VAF": "Normal All DNA VAF",
			  "TUMOR_ALLDNA_VAF": "Tumor All DNA VAF",
			  "RELAPSE_ALLDNA_VAF": "Relapse All DNA VAF",
			  "cgc_gene": "CGC Gene",
			  "drug": "Drug",
			  "drug_pathway": "Drug Pathway"
			},

			//global data
			global: this.$root._data
		}
	},
	methods: {
		//********************************************
		friendly_label(label) {
			return label.replace(/_/g, ' ').toLowerCase();
		},

		//********************************************
		format_data(data, index) {
			let value = data[index];

			return value;
		},

		//********************************************
		select_row(data) {
			this.global.phylogeny_selected_tier = data.tier;
		},

		//********************************************
		deselect_tier() {
			this.global.phylogeny_selected_tier = null;
		},

		//********************************************
		close_table() {
			this.global.phylogeny_show_table = false;
		}
	},
	destroyed() {
	},
	mounted() {
	},
	watch: {
	},
	created() {
		this.details_data;
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


