import Vue from 'vue';
import Router from 'vue-router';

//import test from '@/components/test';
//import sandbox from '@/components/sandbox';
import home from '@/components/home';

import prototype_connect from '@/components/prototype/prototype_connect';

import prototype_gesture from '@/components/prototype/prototype_gesture';
import prototype_genomix from '@/components/prototype/prototype_genomix';
import prototype_genomix_dashboard from '@/components/prototype/prototype_genomix_dashboard';
import prototype_histogram from '@/components/prototype/prototype_histogram';
import prototype_histogram_overlay from '@/components/prototype/prototype_histogram_overlay';
import prototype_interface from '@/components/prototype/prototype_interface';

import prototype_panel from '@/components/prototype/prototype_panel';
import prototype_phylogeny from '@/components/prototype/prototype_phylogeny';
import prototype_pixsure from '@/components/prototype/prototype_pixsure';

import prototype_ui from '@/components/prototype/prototype_ui';
import prototype_voice_command from '@/components/prototype/prototype_voice_command';
import prototype_tile from '@/components/prototype/prototype_tile';

import error from '@/components/error';

Vue.use(Router);

export default new Router({
	routes: [
/*
		{
			path: '/', //all paths are defined without the hash
			name: 'home',
			component: home // change this to directly point to a component library to only allow that access or home for the directory  //prototype_genomix_dashboard  //home

//			props: true,
//			redirect: '' //see documentation if this is needed
		},
*/
		{
			path: '/',
			name: 'home',
			component: prototype_phylogeny
		},
/*
		{
			path: '/connect/',
			name: 'connect',
			component: prototype_connect
		},
		{
			path: '/gesture/',
			name: 'gesture',
			component: prototype_gesture
		},
		{
			path: '/genomix_table/',
			name: 'genomix_table',
			component: prototype_genomix
		},
		{
			path: '/genomix_dashboard/',
			name: 'genomix_dashboard',
			component: prototype_genomix_dashboard
		},
		{
			path: '/histogram/',
			name: 'histogram',
			component: prototype_histogram
		},
		{
			path: '/histogram_overlay/',
			name: 'histogram_overlay',
			component: prototype_histogram_overlay
		},
		{
			path: '/interface/',
			name: 'interface',
			component: prototype_interface
		},
		{
			path: '/panel/',
			name: 'panel',
			component: prototype_panel
		},
		{
			path: '/phylogeny/',
			name: 'phylogeny',
			component: prototype_phylogeny
		},
		{
			path: '/pixsure/',
			name: 'pixsure',
			component: prototype_pixsure
		},
		{
			path: '/ui/',
			name: 'ui',
			component: prototype_ui
		},
		{
			path: '/voice_command/',
			name: 'voice_command',
			component: prototype_voice_command
		},
		{
			path: '/tile/',
			name: 'tile',
			component: prototype_tile
		},
//		{
//			path: '/portfolio/:project?', // will allow this type of urls /#/portfolio/ringr
//			path: '/:project?', // will allow this type of urls /#/ringr  (will never go past to the 404 with this url scheme)
//			name: 'portfolio',
//			component: portfolio
//		},
*/
		{
			path: '*', //404 error
			name: 'error',
			component: error
		}
	]
});


