<template>
	<div class="prototype_voice_command">
		<h5>Voice Commands: {{is_voice_commands_available ? 'ACTIVE' : 'BROWSER NOT SUPPORTED (try Google Chrome instead)'}}</h5>

		<div class="status" v-if="is_voice_commands_available">
			<div class="listening" v-bind:class="{active: is_actively_listening}">{{(is_voice_commands_available && voice_control && voice_control.isObeying()) ? 'LISTENING' : '-'}}</div>
			<div class="speaking" v-bind:class="{active: (voice_control && voice_control.isSpeaking())}">{{(voice_control && voice_control.isSpeaking()) ? 'SPEAKING' : '-'}}</div>
			<div class="matched" v-if="is_voice_commands_available" v-bind:class="{no_match: !is_matched_command}">{{(is_matched_command) ? 'MATCHED COMMAND' : 'COMMAND NOT RECOGNIZED'}}</div>
		</div>

		<br />

		<div class="modes" v-if="is_voice_commands_available">
			<div class="mode" v-for="(item, index) in modes" v-bind:class="{selected: selected_mode == index}">Mode {{(index + 1)}} ({{item.label}})</div>
			<div class="mode" v-for="(item, index) in views" v-bind:class="{selected: selected_view == index}">{{item.label}}</div>
		</div>

		<div class="log">
			<ul>
				<li v-for="entry in log">
					{{entry}}
				</li>
			</ul>
		</div>

		<div v-if="!is_voice_commands_available">VOICE COMMANDS NOT AVAILABLE FOR THIS BROWSER</div>

		<div class="commands" v-if="is_voice_commands_available">
			<div class="title">Try any of the following voice commands:</div>

			<ul>
				<li v-for="command in commands">
					{{voice_control_name}}: {{command.description}}
				</li>
			</ul>

			<div class="note">
				<p>NOTE: this only works in Google Chrome and in order for voice recognition and speaking to work, you must activate the microphone and update the sound settings in the browser.</p>

				<p class="screenshot"><img src="/static/images/settings.png"></p>

				<p>To do this, click on the <em>Not Secure</em> or <em>lock</em> icon in the url bar at the top of this page (or go to <a href="chrome://settings/content/sound?search=sound" target="_blank">chrome://settings/content/sound?search=sound</a>) and then ALLOW SOUND and MICROPHONE to work.</p>

				<p>Some of these restrictions are removed when the website is served as HTTPS.</p>
			</div>
		</div>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

import Artyom from "artyom.js";
import EventBus from '../../event_bus/event_bus.js';

export default {
	name: 'prototype_voice_command',
	components: {
	},
	props: {
	},
	computed: {
	},
	data() {
		return {
			log: [],

			voice_control: null,
			voice_control_name: 'PIXSURE',

			is_voice_commands_available: false,
			is_matched_command: false,
			is_actively_listening: false,

			modes: [
				{label: 'Lipid'},
				{label: 'Cancer'},
				{label: 'Enteroendocrine'},
				{label: 'Pancreatic islets'},
			],
			selected_mode: 0,

			views: [
				{label: 'Scene'},
				{label: 'Tile'}
			],
			selected_view: 0,

			commands: [
				{
					description: 'hi (or hello)',
					indexes: ['hi', 'hello'],
					action: function(index) {
						if (index == 0) {
							EventBus.$emit('hello', {message: 'Well, hi there!'});
						} else {
							EventBus.$emit('hello');
						}
					}
				},
				{
					description: 'mode {any number 1-4}',
					smart: true,
					indexes: ['mode *'],
					action: function(index, mode) {
						EventBus.$emit('switch_mode', {mode: mode});
					}
				},
				{
					description: '{scene or tile} view',
					indexes: ['scene', 'tile'],
					action: function(index, view) {
						EventBus.$emit('switch_view', {index: index, view: view});
					}
				},
				{
					description: 'tell me a joke',
					smart: true,
					indexes: [/tell me (a|another) joke/i],
					action: (index)=>EventBus.$emit('joke')
				},
				{
					description: "my name is {name}, i am {name}, i'm batman",
					smart: true,
					indexes: ['my name is *', 'i am *', "i'm *"],
					action: function(index, name) {
						EventBus.$emit('my_name_is', {name: name});
					}
				},
				{
					description: 'undo',
					indexes: ['undo'],
					action: (index)=>EventBus.$emit('undo')
				},
			],

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		respond(message) {
			//randomly pick a message
			if (Array.isArray(message)) {message = message[Math.floor(Math.random() * message.length)];}

			this.log.unshift(message);

			//speak text as well
			if (this.voice_control.speechSupported()) {this.voice_control.say(message);}
		}
	},
	destroyed() {
		EventBus.$off('hello');
		EventBus.$off('joke');
		EventBus.$off('my_name_is');
		EventBus.$off('switch_mode');

		//stop voice control
		if (this.voice_control) {
			this.voice_control.fatality();
		}
	},
	mounted() {
		//using eventBus to show how this could work (change to a more global setup in the future?)
		EventBus.$on('hello', (parameters)=>{
			let message = (parameters && parameters.message) ? parameters.message : ['Top of the mornin to ya.', 'Hello.'];
			this.respond(message);
		});
		EventBus.$on('joke', (parameters)=>{
			this.respond([
				'A grasshopper walks into a bar…',
				'Knock knock…'
			]);
		});
		EventBus.$on('undo', (parameters)=>{
			this.respond([
				'Undo'
			]);
		});
		EventBus.$on('my_name_is', (parameters)=>{
			if (parameters.name.toLowerCase().trim() == 'batman') {
				this.respond('Holy Graf Zeppelin!'); // I used to think I actually was Batman.');
			} else if (Math.random() > .5) {
				this.respond('Nice to meet you ' + parameters.name + '.');
			} else if (Math.random() > .5) {
				this.respond(parameters.name + ' is a lovely name.');
			} else {
				this.respond('Hi ' + parameters.name + ', nice to meet you.');
			}
		});
		EventBus.$on('switch_mode', (parameters)=>{
			let mode = parameters.mode.toLowerCase().trim();

			//convert known strings to numbers
			let alternatives = {
				'one':1,
				'to':2,
				'too':2,
				'two':2,
				'three':3,
				'for':4,
				'ate':8,
				'zero':0
			};

			if (typeof alternatives[mode] != 'undefined') {mode = alternatives[mode];}

			//by default, pass the string
			let mode_name = 'Mode ' + mode;

			//determine if this is a number
			if (isNaN(mode) == false) {
				let mode_index = parseInt(mode, 10);

				//found a match
				if (mode_index >= 1 && mode_index <= this.modes.length) {
					mode_name = this.modes[mode_index - 1].label + ' mode';

					this.selected_mode = mode_index - 1;
				} else {
					mode_name = 'Mode ' + mode + ' not available';
				}
			} else {
				mode_name = 'Mode ' + mode + ' is currently not available';
			}

			this.respond(mode_name);
		});
		EventBus.$on('switch_view', (parameters)=>{
			this.selected_view = (parameters.index >= 0 && parameters.index < this.views.length) ? parameters.index : 0;
			this.respond(this.views[this.selected_view].label + ' View');
		});

		//setup voice control
		this.voice_control = new Artyom();
		this.is_voice_commands_available = (this.voice_control.recognizingSupported()) ? true : false;

		//voice commands
		this.voice_control.addCommands(this.commands);

		//start listening
		if (this.is_voice_commands_available) {
			this.voice_control.when('COMMAND_MATCHED', ()=>{
				this.is_matched_command = true;
				this.is_actively_listening = false;
			});
			this.voice_control.when('NOT_COMMAND_MATCHED', ()=>{
				this.is_matched_command = false;
				this.is_actively_listening = false;
			});
			this.voice_control.when('TEXT_RECOGNIZED', ()=>{this.is_actively_listening = true;});

			this.voice_control.when('ERROR', (error)=>{
console.error(error);
//				this.is_actively_listening = false;
			});

			this.voice_control.initialize({
				lang:"en-GB",
				continuous: true,
				listen: true, // Start listening after this
				speed: 1, // speed of speech
				mode: "quick", // This parameter is not required as it will be normal by default

				soundex: false,
				name: 'picture', //not Pixure since the voice recognition has a hard time recognizing that
				executionKeyword: null,

				debug: true, // Show what recognizes in the Console
			});

			this.is_actively_listening = true;
		}
	},
	watch: {
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


