<template>
	<div class="prototype prototype_panel">
		<h1>
			Panels

			<div class="grid_layout_settings">
				<div class="button" v-if="preset.parameters.grid_layout" v-on:click="select_preset($event, preset.parameters)" v-for="(preset, index) in presets">{{preset.label}}</div>
			</div>
		</h1>
		<div class="description"></div>

		<div class="panels" v-bind:class="[grid_layout, {is_hover:is_hover}]">
			<div class="panel" v-for="(panel, index) in panels"
				v-bind:class="[{selected:selected_panel == index}]"
				v-bind:style="[{'--cursorX':(follow_me_mode) ? '50%' : (cursor.x * panel.scale) + '%', '--cursorY':(follow_me_mode) ? '50%' : (cursor.y * panel.scale) + '%'}]"
				@mouseover="start_hover($event, index)"
				@mouseleave="stop_hover($event, index)"
				@mousemove="on_move"
				@touchmove="on_move"
				@mousedown="start_drag($event, index)"
				@touchstart="start_drag($event, index)"
				@mouseup="stop_drag"
				@touchend="stop_drag"
			>
				<div class="label">{{panel.label}}</div>
				<div class="zoom" v-on:click.stop="toggle_zoom($event, panel)">{{panel.scale * 100}}%</div>
				<div class="crosshair"></div>

				<div class="konva_stage">
					<v-stage v-bind:ref="'stage-' + index" :config="konva_stage"></v-stage>
				</div>
			</div>
		</div>
<!--
		<div class="konva_stage" v2-bind:style="{backgroundImage: 'url(' + images[selected_image].url + ')'}">
			<v-stage ref="stage" :config="konva_stage"></v-stage>
		</div>
-->

		<div class="presets">
			<div class="button" v-if="!preset.parameters.grid_layout" v-on:click="select_preset($event, preset.parameters)" v-for="(preset, index) in presets">{{preset.label}}</div>
			<div class="button" v-on:click="follow_me_mode = !follow_me_mode" v-bind:class="[{active:follow_me_mode}]">Follow Me Mode</div>

			<div class="setting" style="display: inline-block; vertical-align: middle;">
				<div class="label">Brush Width</div>

				<circle-slider v-model="brush_width" v-bind="slider_config" class="slider"
					v-bind:min="1"
					v-bind:max="30"
					v-bind:stepSize="1"
					v2-bind:progressColor="setting.color"
					v2-bind:knobColor="setting.color"
				></circle-slider>

				<div class="value">
					{{brush_width}}
					<span class="unit">px</span>
				</div>
			</div>
		</div>

		<div class="description">
			<h2>Comments</h2>

			<ul class="disc">
				<li>can change layout to 3, 4, 6, or 9 panels using the buttons at the top</li>
				<li>each panel can change zoom level by clicking on the zoom % in the window. the zoom buttons at the bottom change ALL panels</li>
				<li>each panel can be drawn in…and the others draw automatically</li>
				<li>when "follow me mode" is activated, the panels will move as you move your mouse (or pencil)</li>
				<li>brush width controls the width of the brush and can be changed AFTER drawing</li>
			</ul>

			<p><strong>NOTE: there are a few bugs (especially when swtiching between follow me mode). if/when the display gets out of sync, refresh the page for now to reset this prototype</strong></p>
		</div>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

export default {
	name: 'prototype_panel',
	components: {
	},
	props: {
	},
	computed: {
	},
	data() {
		return {
			//local data
			slider_config: {
				progressColor: "#1dbd80",
				knobColor: "#1dbd80",

				circleWidthRel: 10,
				progressWidthRel: 5,
				knobRadiusRel: 7,
				side: 105
			},

			panels: {
				panel_1: {
					label: 'panel 1',
					scale: .25,
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				},
				panel_2: {
					label: 'panel 2',
					scale: 1,
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				},
				panel_3: {
					label: 'panel 3',
					scale: .5,
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				},
				panel_4: {
					label: 'panel 4',
					scale: 1.5,
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				},
				panel_5: {
					label: 'panel 5',
					scale: 32, //4
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				},
				panel_6: {
					label: 'panel 6',
					scale: 16,
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				},
				panel_7: {
					label: 'panel 7',
					scale: .25,
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				},
				panel_8: {
					label: 'panel 8',
					scale: 8,
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				},
				panel_9: {
					label: 'panel 9',
					scale: .12,
					offset: {
						x: 0,
						y: 0
					},
					stage: null,
					drawing_layer: null,
					line: null
				}
			},

			selected_image: 0,
			images: [
				{url: '/static/images/prototype_panel.jpg'},
//				{url: '/static/images/20180327_MATday15_Tumor2cm_1mm_FOV450um_s3_m1_z156_20us.jpg'},
//				{url: '/static/images/20180327_MATday15_Tumor2cm_1mm_FOV500um_s2_m2_z156_20us.jpg'},
			],

			grid_layout: 'grid_9up',
			scale: 1,
			is_spacebar_pressed: false,
			follow_me_mode: true,
			brush_width: 4,

			cursor: {
				x: 0, //percentage inside the panel
				y: 0
			},
			position_start: { //for moving the image around in the panel
				x: 0,
				y: 0
			},
			is_hover: false,

			presets: [
				{label: '3', parameters: {
					grid_layout: 'grid_3up'
				}},
				{label: '4', parameters: {
					grid_layout: 'grid_4up'
				}},
				{label: '6', parameters: {
					grid_layout: 'grid_6up'
				}},
				{label: '9', parameters: {
					grid_layout: 'grid_9up'
				}},
				{label: '12%', parameters: {
					zoom: .12
				}},
				{label: '25%', parameters: {
					zoom: .25
				}},
				{label: '50%', parameters: {
					zoom: .5
				}},
				{label: '100%', parameters: {
					zoom: 1
				}},
				{label: '200%', parameters: {
					zoom: 2
				}},
				{label: '400%', parameters: {
					zoom: 4
				}},
				{label: '1600%', parameters: {
					zoom: 16
				}},
			],

			konva_stage: {
				width: 200,
				height: 200
			},

			hover_panel: null,
			selected_panel: null,
			is_drawing: false,

			start_pointer_position: null,
			last_pointer_position: null,
			max_distance: 0,

			timer: null,

			//global data
			data: this.$root._data
		}
	},
	methods: {
/*
		//********************************************
		toggle_setting_info(index, force_value) {
			this.settings[index].is_active = (force_value !== undefined) ? force_value : !this.settings[index].is_active;
		},

		//********************************************
		get_value(name) {
			return this.settings[name].value;
		},
*/
		//********************************************
		toggle_zoom(e, panel) {
			e.stopPropagation(); //this didn't work so i added a nodeName test in start_drag which isn't ideal but works for the prototype

			let new_scale = 0;
			let min_scale = 100;
			for (var i=0; i<this.presets.length; i++) {
				let preset = this.presets[i];
				if (typeof preset.parameters.zoom == 'undefined') {continue;}

				//min_scale
				if (preset.parameters.zoom < min_scale) {min_scale = preset.parameters.zoom;}

				//move to the next highest scale
				if (preset.parameters.zoom > panel.scale) {
					new_scale = preset.parameters.zoom;
					break;
				}
			}

			if (new_scale < min_scale) {new_scale = min_scale;}

			if (panel.scale != new_scale) {
				panel.scale = new_scale;

				let stage = panel.stage;
				stage.scale({
					x: panel.scale,
					y: panel.scale
				});

				stage.draw();
			}
		},

		//********************************************
		start_hover(e, index) {
			this.hover_panel = index;
			this.is_hover = true;
		},

		//********************************************
		stop_hover(e) {
			this.hover_panel = null;
			this.is_hover = false;
		},

		//********************************************
		on_move(e) {
//console.log(e.offsetX + ' :: ' + e.offsetY);
			let target = e.target;

			if (typeof e.offsetX != 'undefined') {
				var mouse_x = e.offsetX;
				var mouse_y = e.offsetY;
			} else {
				let rect = e.target.getBoundingClientRect();
				var mouse_x = e.targetTouches[0].pageX - rect.left - 5; //not quite right as it still has some offset. hard coded around it for this prototype
				var mouse_y = e.targetTouches[0].pageY - rect.top - 40;

//				var mouse_x = e.touches[0].pageX - e.touches[0].target.offsetLeft;
//				var mouse_y = e.touches[0].pageY - e.touches[0].target.offsetTop;
			}

			let width = target.clientWidth;
			let height = target.clientHeight;

			let offset_x = (this.hover_panel) ? this.panels[this.hover_panel].stage.offsetX() : 0;
			let offset_y = (this.hover_panel) ? this.panels[this.hover_panel].stage.offsetY() : 0;
			let scale = (this.hover_panel) ? this.panels[this.hover_panel].scale : 1;

			let x = mouse_x + (offset_x * scale);
			let y = mouse_y + (offset_y * scale);

//console.log(x + ' :: ' + y + ' --- ' + offset_x + ' :: ' + offset_y + ' :: ' + scale);
			//convert current panel scale to 100% base zoom level
			this.cursor.x = x * 100 / width / scale;
			this.cursor.y = y * 100 / height / scale;

			//modifiers

			if (this.is_spacebar_pressed) {
				//move
			} else if (this.is_drawing) {
				//draw
				this.add_drawing_point();
			}
		},

		//********************************************
		start_timer() {
			this.stop_timer();

			this.timer = setTimeout(()=>{
//console.error('timer done!');
			}, this.settings['delay'].value);
		},

		//********************************************
		stop_timer() {
			if (this.timer) {
				clearTimeout(this.timer);
				this.timer = null;
			}
		},

		//********************************************
		clear_drawing_layer() {
			for (let index in this.panels) {
				if (this.panels[index].line) {
//					this.panels[index].line.clear();
					this.panels[index].line.points([]);
					this.panels[index].line.draw();
				} //clear point array-

				if (this.panels[index].drawing_layer) {
					this.panels[index].drawing_layer.clear();
					this.panels[index].drawing_layer.cache();
				}
			}
		},

		//********************************************
		add_drawing_point() {
			for (let index in this.panels) {
				let stage = this.panels[index].stage;

				let width = stage.width();
				let height = stage.height();

				let x = this.cursor.x; // / this.panels[index].scale;
				let y = this.cursor.y; // / this.panels[index].scale;

				this.panels[index].line.points(this.panels[index].line.points().concat([width * (x / 100), height * (y / 100)]));

				this.panels[index].drawing_layer.imageSmoothingEnabled(false);
				this.panels[index].line.draw();
				this.panels[index].drawing_layer.cache();
//this.panels[index].line.cache({imageSmoothingEnabled: false}); //also helps create pixel render

				//if follow me, then move the panel position to keep the cursor in the middle on the other screens
				if (this.follow_me_mode) {
//					for (let index in this.panels) {
//						let stage = this.panels[index].stage;

					if (this.selected_panel == index) {
						//nothing to do on the panel we are drawing on
						stage.draw(); //update since we're using image cache to render pixels
					} else {
						//move the other panels

//						this.panels[index].offset.x = (x * this.panels[index].scale) - (width / this.panels[index].scale / 2);
//						this.panels[index].offset.y = (y * this.panels[index].scale) - (height / this.panels[index].scale / 2);
//						this.panels[index].offset.x = (width / 2) - (x * this.panels[index].scale);
//						this.panels[index].offset.y = (height / 2) - (y * this.panels[index].scale);
						this.panels[index].offset.x = (x / 100 * width) - (width / this.panels[index].scale / 2);
						this.panels[index].offset.y = (y / 100 * height) - (height / this.panels[index].scale / 2);

						stage.offsetX(this.panels[index].offset.x); // / this.panels[index].scale);
						stage.offsetY(this.panels[index].offset.y); // / this.panels[index].scale);

						stage.draw();
					}
//					}
				}
			}
		},

		//********************************************
		start_drag(e, index) {
			let target = e.target;
			if (target.nodeName == 'DIV') {return;} //clicked on something besides the canvas

			//force this here for touch events (which won't have a hover)
			if (!this.hover_panel) {
				this.hover_panel = index;
				this.on_move(e);
			}

			this.selected_panel = index;

			this.position_start.x = this.cursor.x;
			this.position_start.y = this.cursor.y;

			this.clear_drawing_layer();
			this.add_drawing_point();

//			this.drag_focus = e.target; //not sure we need to track this
			this.is_drawing = true;
		},

		//********************************************
		stop_drag() {
//			this.drag_focus = null;
//			this.hover_panel = null;
			this.is_drawing = false;
		},

		//********************************************
		select_preset(e, parameters) {
			if (typeof parameters['grid_layout'] != 'undefined') {
				this.grid_layout = parameters['grid_layout'];

				Vue.nextTick(()=>{
					this.on_resize();

					for (let index in this.panels) {
//						this.panels[index].stage.draw();
					}
				});
			} else if (typeof parameters['zoom'] != 'undefined') {
				for (let index in this.panels) {
					//change if shift key or this is the selected panel
//					if (e.shiftKey || this.selected_panel == index) {
						this.panels[index].scale = parameters['zoom'];

						let stage = this.panels[index].stage;

						stage.scale({
							x: this.panels[index].scale,
							y: this.panels[index].scale
						});

						stage.draw();
//					}
				}
			}
		},
/*
		//********************************************
		on_scale() {
			for (let index in this.panels) {
				let stage = this.panels[index].stage;
				stage.scale({
					x: this.scale,
					y: this.scale
				});

				stage.draw();
			}
		},
*/
		//********************************************
		on_resize() {
			for (let index in this.panels) {
				let stage = this.panels[index].stage;

				let container = stage.container();
				let width = container.clientWidth;
				let height = container.clientHeight;

				stage.width(width);
				stage.height(height);

				stage.draw(); //not sure this is needed
			}
		},

		//********************************************
		on_keydown(e) {
			if (e.keyCode == '32') { //change to switch?
				this.is_spacebar_pressed = true;
				e.preventDefault();
			}
		},

		//********************************************
		on_keyup(e) {
			if (e.keyCode == '32') { //change to switch?
				this.is_spacebar_pressed = false;
				e.preventDefault();
			}
		}
	},
	destroyed() {
		window.removeEventListener('resize', this.on_resize);
		window.removeEventListener('mouseup', this.stop_drag);

		window.removeEventListener('keydown', this.on_keydown);
		window.removeEventListener('keyup', this.on_keyup);
	},
	mounted() {
		//stop drag event
		window.addEventListener('mouseup', this.stop_drag);

		//
		for (let index in this.panels) {
			this.panels[index].stage = this.$refs['stage-' + index][0].getStage();
			this.panels[index].stage.scale({
				x: this.panels[index].scale,
				y: this.panels[index].scale
			});

			//load image
			let image_layer = new Konva.Layer(); //no need to reference this since we only do it once in this prototype

			this.panels[index].stage.add(image_layer);

			Konva.Image.fromURL(this.images[0].url, (image)=>{
				image.setAttrs({
					x: 0,
					y: 0
				});

				image_layer.imageSmoothingEnabled(false); //keep pixels
				image_layer.add(image);
				image_layer.draw();
				image_layer.cache(); //also helps create pixel render
			});

			//setup drawing layer
			this.panels[index].drawing_layer = new Konva.Layer();
//			this.panels[index].drawing_layer.imageSmoothingEnabled(false);

			this.panels[index].stage.add(this.panels[index].drawing_layer);

			//setup line
			this.panels[index].line = new Konva.Line({
				x: 0,
				y: 0,
				points: [],
				stroke: '#f00',
				strokeWidth: this.brush_width,
				tension: 0,
//				closed: true,
				lineJoin: 'round',
				lineCap: 'round',
//				imageSmoothingEnabled: false
			});
			this.panels[index].drawing_layer.add(this.panels[index].line);

			this.panels[index].stage.draw();
		}

		//keyboard events
		window.addEventListener('keydown', this.on_keydown);
		window.addEventListener('keyup', this.on_keyup);

		//reseize event
		this.on_resize();
		window.addEventListener('resize', this.on_resize); //add timer into this to gain better performance on resize (instead of every pixel resized)
	},
	watch: {
		brush_width: function(new_width, old_width) {
			for (let index in this.panels) {
				let stage = this.panels[index].stage;

				//change stroke width
				this.panels[index].line.strokeWidth(new_width);

				stage.draw();

				this.panels[index].drawing_layer.cache();
			}
		}
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


