<template>
	<div class="prototype">
		<h1>Determine Gesture: <span class="mode">{{mode}}</span></h1>
		<div class="description"></div>

		<div class="konva_stage" v-bind:style="{backgroundImage: 'url(' + images[selected_image].url + ')'}">
			<v-stage ref="stage" :config="konva_stage"></v-stage>
		</div>

		<div class="setting_group">
			<div class="setting radio_buttons">
				<div class="label">Choose Image</div>

				<div class="buttons">
					<div v-for="(image, index) of images" class="radio_button" v-bind:class="{selected: selected_image == index}">
						<input type="radio" :id="'image_' + index" :value="index" v-model="selected_image" v-on:click="selected_image = index"><label :for="'image_' + index">Image {{index}}</label></input>
					</div>
				</div>
			</div>

			<div class="setting" v-for="(setting, index) of settings" v-bind:class="[{active: setting.is_active}]" v-on:mouseover="selected_setting = index" v-on:mouseout="selected_setting = null">
				<div class="label" v-on:click="toggle_setting_info(index)">
					<div v-html="setting.label"></div>
<!--					<div class="description" v-if="setting.description" v-html="setting.description"></div>-->
				</div>

				<circle-slider v-model="setting.value" v-bind="slider_config" class="slider"
					v-bind:min="setting.min"
					v-bind:max="setting.max"
					v-bind:stepSize="setting.step"
					v-bind:progressColor="setting.color"
					v-bind:knobColor="setting.color"
				></circle-slider>

				<div class="progress_bar" v-bind:style="{width: setting.progress_bar + '%'}" v-if="setting.progress_bar"></div>

				<div class="value">
					{{setting.value}}
					<span class="unit" v-if="setting.unit" v-html="setting.unit"></span>
				</div>
			</div>
		</div>

		<div class="notes" v-if="selected_setting !== null" v-html="settings[selected_setting].description"></div>
	</div>
</template>

<script>
//*********************************************************

//import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

export default {
	name: 'prototype_gesture',
	components: {
	},
	props: {
	},
	computed: {
		min_snap_radius: function() {
			return (this.settings['brush_size'].value >= this.settings['snap_radius'].value) ? this.settings['brush_size'].value : this.settings['snap_radius'].value;
		}
	},
	data() {
		return {
			//local data
			slider_config: {
				progressColor: "#1dbd80",
				knobColor: "#1dbd80",

				circleWidthRel: 10,
				progressWidthRel: 5,
				knobRadiusRel: 7,
				side: 105
			},

			selected_image: 0,
			images: [
				{url: '/static/images/20180327_MATday15_Tumor2cm_1mm_FOV450um_s3_m1_z156_20us.jpg'},
				{url: '/static/images/20180327_MATday15_Tumor2cm_1mm_FOV500um_s2_m2_z156_20us.jpg'},
			],

			selected_setting: null,
			settings: {
				delay: {
					value: 600, //200, //100
					label: 'Click Delay',
					description: 'Amount of time to wait after clicking before deciding if this guesture is a circle.',
					min: 50,
					max: 1250,
					step: 10,
					color: '#0f8',
					unit: 'ms',
					is_active: false,

					progress_bar: 0
				},
				circle_trigger_radius: {
					value: 4, //2
					label: 'Circle Trigger',
					description: 'Max distance cursor can travel from start point before it is no longer a circle gesture. ONLY tested for during the delay, ignored after that.<br /><br />Indicated in the example by a <span style="color: #08f;">blue circle</span>.',
					min: 1,
					max: 100,
					step: 1,
					color: '#08f',
					unit: 'px',
					is_active: false
				},
				snap_radius: {
					value: 10, //8
					label: 'Snap-to Radius',
					description: '<strong>Minimum distance</strong> from start point the cursor must return to in order to enclose the area, used primarily for small brush sizes. If the brush size is a larger value, the brush size will be used for the snap-to radius instead.<br /><br />Indicated in the example by a <span style="color: #f55;">red circle</span>.',
					min: 1,
					max: 50,
					step: 1,
					color: '#f55',
					unit: 'px',
					is_active: false
				},
				brush_size: {
					value: 15,
					label: 'Brush Size',
					description: 'Size of the brush stroke. Will also control the snap-to radius if larger than the minimum snap-to radius size.<br /><br />Indicated in the example by a <span style="color: #f8f;">pink circle</span>.',
					min: 1,
					max: 150,
					step: 1,
					color: '#f8f',
					unit: 'px',
					is_active: false
				},
/*
				circle_trigger_radius3: {
					value: 30,
					label: 'Snap-to Radius',
					description: 'Max distance from start point the cursor must return to to enclose the area.',
					min: 1,
					max: 100,
					step: 1,
					color: '#aaa',
					unit: 'px',
					is_active: false
				},
				circle_trigger_radius4: {
					value: 40,
					label: 'Snap-to Radius',
					description: 'Max distance from start point the cursor must return to to enclose the area.',
					min: 1,
					max: 100,
					step: 1,
					color: '#ff8',
					unit: 'px',
					is_active: false
				}
*/
			},

			konva_stage: {
				width: 600,
				height: 600
			},

			points: [23, 20, 23, 160],

			stage: null,
			layer: null,
			circle_helper: null,

			canvas: null, //used for painting (will clear after each mouseup)
			context: null, //used for painting image canvas
			is_painting: false,

			mode: 'line', //circle, line, area
			start_pointer_position: null,
			last_pointer_position: null,
			max_distance: 0,

			gesture_timer: null,

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		toggle_setting_info(index, force_value) {
			this.settings[index].is_active = (force_value !== undefined) ? force_value : !this.settings[index].is_active;
		},

		//********************************************
		get_value(name) {
			return this.settings[name].value;
		},

		//********************************************
		start_gesture_timer() {
			this.stop_gesture_timer();

/*
()=>{
						return (this.gesture_timer) ? 100 : 12;
					}
*/
			this.gesture_timer = setTimeout(()=>{
console.error('gesture timer done!');
				this.mode = 'circle';

				this.clear_canvas();
				this.circle_helper.stroke('#888');

				this.layer.batchDraw();

				//circle should already be at the start spot, no need to do anything else
			}, this.settings['delay'].value);
		},

		//********************************************
		stop_gesture_timer() {
			if (this.gesture_timer) {
				clearTimeout(this.gesture_timer);
				this.gesture_timer = null;

				this.circle_helper.radius(this.min_snap_radius); //this.settings['snap_radius'].value);
				this.circle_helper.stroke(this.settings['snap_radius'].color); //delay
			}
		},

		//********************************************
		clear_canvas() {
			if (this.context && this.canvas) {this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);}
		}
	},
	mounted() {
		this.stage = this.$refs['stage'].getStage();

		// add the layer to the stage
		this.layer = new Konva.Layer();
		this.stage.add(this.layer);

//https://stackoverflow.com/questions/35648163/konvajs-change-points-of-line-when-dragged
//https://konvajs.org/docs/sandbox/Free_Drawing.html
		//create drawing area
		this.canvas = document.createElement('canvas');
		this.canvas.width = this.stage.width();
		this.canvas.height = this.stage.height();

		//created canvas we can add to layer as "Konva.Image" element
		var image = new Konva.Image({
			image: this.canvas,
			x: 0,
			y: 0
		});
		this.layer.add(image);

		//create circle for start and snap-to
		this.circle_helper = new Konva.Circle({
			x: 0,
			y: 0,
			radius: this.settings['brush_size'].value / 2,
			stroke: this.settings['brush_size'].color,
			strokeWidth: 2,
//			visible: false
		});
		this.circle_helper.listening(false);
		this.layer.add(this.circle_helper);

		//draw to stage
		this.stage.draw();

		//access the context element
		this.context = this.canvas.getContext('2d');
		this.context.strokeStyle = '#ff5';
		this.context.lineJoin = 'round';
		this.context.lineWidth = this.settings['brush_size'].value;

		//events
		image.on('mousedown touchstart', ()=>{
			this.is_painting = true;
			this.mode = 'line';

			this.start_pointer_position = this.stage.getPointerPosition();
			this.last_pointer_position = this.stage.getPointerPosition();
			this.max_distance = 0;

			//draw circle gesture bounds
//			  this.context.moveTo(this.last_pointer_position.x, this.last_pointer_position.y);

			this.context.lineWidth = this.settings['brush_size'].value;
			this.circle_helper.absolutePosition(this.start_pointer_position); //this.start_pointer_position.x, this.start_pointer_position.y);
			this.circle_helper.radius(this.settings['circle_trigger_radius'].value);

			this.start_gesture_timer();
			this.clear_canvas();

			this.circle_helper.visible(true);
			this.circle_helper.stroke(this.settings['circle_trigger_radius'].color); //delay

			this.layer.batchDraw();
		});

		this.stage.on('mouseup touchend', ()=>{
			this.is_painting = false;

			var position = this.stage.getPointerPosition();

			//was this a quick click (and circle gesture timer is still going). if so, then drop on a circle?
			if ((this.gesture_timer && this.max_distance < this.settings['circle_trigger_radius'].value) || this.mode == 'circle') {
				//create dot (aka, a circle the size of the brush_size setting or distance)
				if (this.mode == 'circle') {
					var size = Math.sqrt(Math.pow(position.x - this.start_pointer_position.x, 2) + Math.pow(position.y - this.start_pointer_position.y, 2));
				} else {
					var size = this.settings['brush_size'].value / 2;
				}

				this.context.beginPath();
				this.context.arc(this.start_pointer_position.x, this.start_pointer_position.y, size, 0, 2 * Math.PI, false);
				this.context.fillStyle = '#ff5';
				this.context.strokeStyle = '#ff5';
				this.context.fill();
//				this.context.closePath();
			} else if (this.mode == 'area') {
/*
				this.context.fillStyle = '#ff5';
				this.context.strokeStyle = '#ff5';
				this.context.fill();
*/
			}

//			this.layer.batchDraw();

			this.stop_gesture_timer();

			this.circle_helper.absolutePosition(position);
			this.circle_helper.radius(this.settings['brush_size'].value / 2);
			this.circle_helper.stroke(this.settings['brush_size'].color);
//			this.circle_helper.visible(false);
			this.start_pointer_position = null;

			this.layer.batchDraw();
		});

		//core drawing function
		this.stage.on('mousemove touchmove', ()=>{
			var position = this.stage.getPointerPosition();

			if (!this.is_painting) {
				//move circle helper
				this.circle_helper.absolutePosition(position);
				this.layer.batchDraw();

				return;
			}

			if (this.mode === 'brush') {
				this.context.globalCompositeOperation = 'source-over';
			} else if (this.mode === 'eraser') {
				this.context.globalCompositeOperation = 'destination-out';
			}

			this.context.beginPath();

			//last position
			var local_position = {
				x: this.last_pointer_position.x - image.x(),
				y: this.last_pointer_position.y - image.y()
			};

			//move to the last position
			this.context.moveTo(local_position.x, local_position.y);

			local_position = {
				x: position.x - image.x(),
				y: position.y - image.y()
			};

			//determine distance traveled if currently in circle mode or testing for the circle gesture
			//https://ncalculators.com/geometry/length-between-two-points-calculator.htm
			var distance = Math.sqrt(Math.pow(local_position.x - this.start_pointer_position.x, 2) + Math.pow(local_position.y - this.start_pointer_position.y, 2));
			if (distance > this.max_distance) {this.max_distance = distance;}

			//if cursor has moved enough, then we are not in circle mode and no need to test for that gesture
			if (this.start_gesture_timer && distance >= this.settings['circle_trigger_radius'].value) {this.stop_gesture_timer();}

			//draw on page
			if (this.mode == 'circle') {
//				this.circle_helper.move(local_position.x, local_position.y);
//console.log('resize circle: ' + distance);
				this.circle_helper.radius(distance);
			} else {
				this.context.lineTo(local_position.x, local_position.y);
				this.context.closePath();
				this.context.stroke();

				this.mode = (!this.gesture_timer && (distance <= this.min_snap_radius)) ? 'area' : 'line'; //this.settings['snap_radius'].value
			}

			this.last_pointer_position = position;

			this.layer.batchDraw();
		});
	},
	watch: {
		'settings.brush_size.value': function(new_value, old_value) {
			if (this.is_painting) {return;}

			this.circle_helper.radius(new_value / 2);
			this.layer.batchDraw();
		}
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


