<template>
	<div class="prototype_pixsure">
		<div class="photo" v-if="selected_image" v-bind:style="{backgroundImage: 'url(' + selected_image + ')'}" v-bind:class="{is_zoomed_in: is_zoomed_in}" v-on:click="on_click"></div>

		<div class="custom_views">
			<div class="view add_view" v-on:click="on_add_view"></div>

			<div class="view" v-for="(view, index) in views" v-bind:class="{active: selected_view == view}" v-on:click="on_select_view(view)" v-bind:style="{backgroundImage: 'url(' + view.url + ')'}">
				<div class="label">{{view.label}}</div>
			</div>
		</div>

		<div class="control_bar">
			<div class="icon icon_menu"><div class="label">=</div></div>
			<div class="icon icon_tool"><div class="label">::</div></div>

			<div class="slider_bar">
				<div class="slide" v-for="(slide, index) in slides" v-bind:class="{active: selected_slide == slide}" v-on:click="on_select_slide(slide)">
					<div class="label">{{slide.label}}<span class="shortcut">(CTRL-{{index + 1}})</span></div>
				</div>
			</div>

			<div class="icon icon_undo">
				<div class="label">&lt;</div>

				<div class="icon icon_redo offset disabled"><div class="label">&gt;</div></div>
			</div>

			<div class="icon_spacer"></div>

			<div class="icon icon_redo">
				<div class="label" v-on:click="objects.push(true);">+</div>

				<div class="icon icon_objects offset"><div class="label">
					<span class="title">Object</span>
					{{objects.length + 1}}
				</div></div>
			</div>
		</div>

		<div class="feedback_bar" v-bind:class="{active: show_feedback_bar}" v-on:click="toggle_feedback_bar">
			<div class="bar">
				<div class="label">Epithelial<div class="match">85% match</div></div>
				<div class="graphs">
					<div class="graph_block">
						<div class="label">Selection</div>
						<div class="graph"></div>
					</div>
					<div class="graph_block">
						<div class="label">Average</div>
						<div class="graph"></div>
					</div>
					<div class="graph_block">
						<div class="label">Most similar</div>
						<div class="graph"></div>
					</div>
				</div>
			</div>

			<div class="feedback_container">
				<div class="control">
					<div class="label">Hue</div>
					<div class="color_picker histogram" v-on:mousedown="start_drag($event, 'histogram')" v-on:mouseup="stop_drag($event)" v-on:click.stop="">
						<div class="min" v-bind:style="{left: (histogram.min / histogram.total * 100) + '%'}"><div class="label">{{histogram.min}}</div></div>
						<div class="max" v-bind:style="{left: (histogram.max / histogram.total * 100) + '%'}"><div class="label">{{histogram.max}}</div></div>
						<svg ref="histogram_svg" />
					</div>
				</div>

				<div class="control">
					<div class="label">Brightness</div>
					<div class="color_picker brightness" v-on:mousedown="start_drag($event, 'brightness')" v-on:mouseup="stop_drag($event)" v-on:click.stop="">
						<div class="min" v-bind:style="{left: (brightness.min / brightness.total * 100) + '%'}"><div class="label">{{brightness.min}}</div></div>
						<div class="max" v-bind:style="{left: (brightness.max / brightness.total * 100) + '%'}"><div class="label">{{brightness.max}}</div></div>
						<svg ref="brightness_svg" />
					</div>
				</div>

				<div class="description">
					<p>Panel will have settings for various selector types (hue, brightness, UV, which views to include, etc.).</p>
					<p>Needs to be able to add layers that filter out the selection, like heat maps, topography, temoral (changes over time), infrared, etc. which will be useful for the snow cloud problem.</p>

					<p>
						<div class="button">Cancel</div>
						<div class="button">Save</div>
					</p>
				</div>
			</div>
		</div>

		<div class="stats">
			<div class="stat" v-for="(stat, index) in stats">
				<div class="label">{{stat.label}}</div>
				<div class="value">{{stat.value.toLocaleString()}}</div>
			</div>
		</div>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

export default {
	name: 'prototype_pixsure',
	components: {
	},
	props: {
	},
	computed: {
	},
	data() {
		return {
			//local data
			slider_config: {
				progressColor: "#1dbd80",
				knobColor: "#1dbd80",

				circleWidthRel: 10,
				progressWidthRel: 5,
				knobRadiusRel: 7,
				side: 105
			},

			objects: [],

			stats: [
				{label: 'Cancer', value: 1022},
				{label: 'Epithelial', value: 10274},
				{label: 'Lipid', value: 5835},
				{label: 'Stroma', value: 50146}
			],

			slides: [
				{label: 'Default', url: '/static/images/prototype_pixsure.jpg'},
				{label: 'Red', url: '/static/images/prototype_pixsure_red.jpg'},
				{label: 'UV', url: '/static/images/prototype_pixsure_uv.jpg'},
				{label: 'Heat map', url: '/static/images/prototype_pixsure_heat.jpg'}
			],
			selected_slide: null,

			views: [
				{label: 'Red Channel', url: '/static/images/prototype_pixsure_red.jpg'},
				{label: 'Zoom', url: '/static/images/prototype_pixsure_zoom.jpg'},
				{label: 'UV', url: '/static/images/prototype_pixsure_uv.jpg'}
			],
			selected_view: null,

			selected_image: null,
			images: [
				{url: '/static/images/prototype_panel.jpg'},
//				{url: '/static/images/20180327_MATday15_Tumor2cm_1mm_FOV450um_s3_m1_z156_20us.jpg'},
//				{url: '/static/images/20180327_MATday15_Tumor2cm_1mm_FOV500um_s2_m2_z156_20us.jpg'},
			],

			slider_name: null,
			slider_parameter: null,

			histogram: {
				min: 168,
				max: 319,
				total: 360,
				parameter: null //reference during a drag across this slider to know which parameter to control
			},
			brightness: {
				min: 18,
				max: 99,
				total: 100,
				parameter: null
			},

			is_zoomed_in: false,
			show_feedback_bar: false,

			click_timer: null,
			is_spacebar_pressed: false,

			konva_stage: {
				width: 200,
				height: 200
			},

			timer: null,

			//global data
			data: this.$root._data
		}
	},
	methods: {
		//********************************************
		on_click(e) {
			this.is_double_click(e).then(
				double_click => {
					this.is_zoomed_in = !this.is_zoomed_in;
				},
				single_click => {

				}
			);
		},

		//********************************************
		is_double_click(e) {
			//wait for another click, otherwise, fire the click function
			return new Promise((double_click, single_click) => {
				//double click
				if (this.click_timer) {
					clearTimeout(this.click_timer);
					double_click();
				}

				//wait to determine if single click
				this.click_timer = setTimeout(() => {
					this.click_timer = null;
					single_click();
				}, 300);
			});
		},

		//********************************************
		on_select_view(view) {
			//toggle
			this.selected_view = (this.selected_view != view) ? view : null;
		},

		//********************************************
		on_add_view(e) {
			this.views.push({
				label: 'Custom View ' + (this.views.length - 2), //to fake 1+
				url: '/static/images/prototype_pixsure.jpg'
			});
		},

		//********************************************
		on_select_slide(slide) {
			//toggle
			this.selected_slide = slide;
			this.selected_image = this.selected_slide['url'];
		},

		//********************************************
		start_drag(e, slider_name) {
			var target = e.target;

			this.slider_name = slider_name;
			this.slider_parameter = 'min';

			var total = this[this.slider_name].total;

			//determine which property to control
			var cursor_percent = e.offsetX / 360 * 100; //cursor location
			var min_percent = this[this.slider_name].min / total * 100;
			var max_percent = this[this.slider_name].max / total * 100;

			if (cursor_percent <= min_percent) {
				this.slider_parameter = 'min';
			} else if (cursor_percent >= max_percent) {
				this.slider_parameter = 'max';
			} else {
				//determine which is closest
				if (max_percent - cursor_percent < cursor_percent - min_percent) {this.slider_parameter = 'max';}
			}

			['mousemove', 'touchmove'].forEach((event)=>{
				target.addEventListener(event, this.drag_move);
			});
		},

		//********************************************
		stop_drag(e) {
			var target = e.target;

			['mousemove', 'touchmove'].forEach((event)=>{
				target.removeEventListener(event, this.drag_move);
			});
		},

		//********************************************
		drag_move(e) {
			var new_value = (e.offsetX / 360 * this[this.slider_name].total);

			if (this.slider_parameter == 'min' && new_value > this[this.slider_name].max) {
				new_value = this[this.slider_name].max;
			} else if (this.slider_parameter == 'max' && new_value < this[this.slider_name].min) {
				new_value = this[this.slider_name].min;
			}

			if (new_value != this[this.slider_name][this.slider_parameter]) {
				this[this.slider_name][this.slider_parameter] = Math.floor(new_value * 10) / 10;
				this.refresh_mask();
			}
		},

		//********************************************
		toggle_feedback_bar() {
			this.show_feedback_bar = !this.show_feedback_bar;
		},

		//********************************************
		start_timer() {
			this.stop_timer();

			this.timer = setTimeout(()=>{
//console.error('timer done!');
			}, this.settings['delay'].value);
		},

		//********************************************
		stop_timer() {
			if (this.timer) {
				clearTimeout(this.timer);
				this.timer = null;
			}
		},

		//********************************************
		on_resize() {
			//resize stage
		},

		//********************************************
		on_keydown(e) {
			if (e.keyCode == '32') {
				this.is_spacebar_pressed = true;
				e.preventDefault();
			}
		},

		//********************************************
		on_keyup(e) {
			if (e.keyCode == '32') {
				this.is_spacebar_pressed = false;
				e.preventDefault();
			}
		},

		//********************************************
		//other js library to consider  https://gist.github.com/mjackson/5311256
		//https://css-tricks.com/converting-color-spaces-in-javascript/

//FUTURE: look into konvaJS filters for HSL https://konvajs.org/api/Konva.Filters.html#.HSL

		HSLToHex(h, s, l) {
			s /= 100;
			l /= 100;

			let c = (1 - Math.abs(2 * l - 1)) * s,
			x = c * (1 - Math.abs((h / 60) % 2 - 1)),
			m = l - c/2,
			r = 0,
			g = 0,
			b = 0;

			if (0 <= h && h < 60) {
				r = c; g = x; b = 0;
			} else if (60 <= h && h < 120) {
				r = x; g = c; b = 0;
			} else if (120 <= h && h < 180) {
				r = 0; g = c; b = x;
			} else if (180 <= h && h < 240) {
				r = 0; g = x; b = c;
			} else if (240 <= h && h < 300) {
				r = x; g = 0; b = c;
			} else if (300 <= h && h < 360) {
				r = c; g = 0; b = x;
			}

			// Having obtained RGB, convert channels to hex
			r = Math.round((r + m) * 255).toString(16);
			g = Math.round((g + m) * 255).toString(16);
			b = Math.round((b + m) * 255).toString(16);

			// Prepend 0s, if necessary
			if (r.length == 1) {r = "0" + r;}
			if (g.length == 1) {g = "0" + g;}
			if (b.length == 1) {b = "0" + b;}

			return "#" + r + g + b;
		},
		RGBToHSL(r, g, b) {
			// Make r, g, and b fractions of 1
			r /= 255;
			g /= 255;
			b /= 255;

			// Find greatest and smallest channel values
			let cmin = Math.min(r,g,b),
			cmax = Math.max(r,g,b),
			delta = cmax - cmin,
			h = 0,
			s = 0,
			l = 0;

			// Calculate hue
			// No difference
			if (delta == 0)
			h = 0;
			// Red is max
			else if (cmax == r)
			h = ((g - b) / delta) % 6;
			// Green is max
			else if (cmax == g)
			h = (b - r) / delta + 2;
			// Blue is max
			else
			h = (r - g) / delta + 4;

			h = Math.round(h * 60);

			// Make negative hues positive behind 360°
			if (h < 0)
			h += 360;

			// Calculate lightness
			l = (cmax + cmin) / 2;

			// Calculate saturation
			s = delta == 0 ? 0 : delta / (1 - Math.abs(2 * l - 1));

			// Multiply l and s by 100
			s = +(s * 100).toFixed(1);
			l = +(l * 100).toFixed(1);

			return {h:h, s:s, l:l}; //[h,s,l]; //"hsl(" + h + "," + s + "%," + l + "%)";
		}
	},
	destroyed() {
		window.removeEventListener('resize', this.on_resize);

		window.removeEventListener('keydown', this.on_keydown);
		window.removeEventListener('keyup', this.on_keyup);
	},
	mounted() {
		//keyboard events
		window.addEventListener('keydown', this.on_keydown);
		window.addEventListener('keyup', this.on_keyup);

		this.on_select_slide(this.slides[0]);

		//draw the histogram svg (color histogram)
		var svgns = "http://www.w3.org/2000/svg";

		//change to gradient instead of lots of rectangles
		for (var x=0; x<360; x++) {
			var rect = document.createElementNS(svgns, 'rect');

			var color = this.HSLToHex(x, 100, 50);

			rect.setAttributeNS(null, 'x', x * 1);
			rect.setAttributeNS(null, 'y', 0);
			rect.setAttributeNS(null, 'width', 1); //fixed at 2px wide for now...eventually change to support responsive design
			rect.setAttributeNS(null, 'height', 40);
			rect.setAttributeNS(null, 'fill', color); //'#' + Math.round(0xffffff * Math.random()).toString(16));

			this.$refs['histogram_svg'].appendChild(rect);
		}

		//change to gradient instead of lots of rectangles
		//draw the brightness svg (brightness histogram)
		for (var x=0; x<360; x++) {
			var rect = document.createElementNS(svgns, 'rect');

			var levels = Math.round(0xff * (x / 360)).toString(16);
			if (levels.length == 1) {levels = '0' + levels;}
			var color = '#' + levels + levels + levels; //Math.round(0xffffff * (x / 360)).toString(16);

			rect.setAttributeNS(null, 'x', x * 1);
			rect.setAttributeNS(null, 'y', 0);
			rect.setAttributeNS(null, 'width', 1); //fixed at 2px wide for now...eventually change to support responsive design
			rect.setAttributeNS(null, 'height', 40);
			rect.setAttributeNS(null, 'fill', color); //'#' + Math.round(0xffffff * Math.random()).toString(16));

			this.$refs['brightness_svg'].appendChild(rect);
		}

		//reseize event
		this.on_resize();
		window.addEventListener('resize', this.on_resize); //add timer into this to gain better performance on resize (instead of every pixel resized)
	},
	watch: {
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


